import React, { Component } from "react"
import { connect } from 'react-redux';
import {
  FormControl,
  Select,
  MenuItem
} from "@material-ui/core"
import { updateFilterPeriod } from 'actions/graph'

class TimeRangeSelector extends Component {
  render() {
    return (
      <FormControl variant="filled">
        <Select
          labelId="demo-simple-select-filled-label"
          id="demo-simple-select-filled"
          value={this.props.graph.filterPeriod}

          onChange={(event, newFilterPeriod) => this.props.updateFilterPeriod(newFilterPeriod.props.value)}
        >
          <MenuItem value='year'>YEAR</MenuItem>
          <MenuItem value='season'>HALF</MenuItem>
          <MenuItem value='quarter'>QUARTER</MenuItem>
          <MenuItem value='month'>MONTH</MenuItem>
          <MenuItem value='week'>WEEK</MenuItem>
        </Select>
      </FormControl>
    )
  }
}


const mapStateToProps = (state, _ownProps) => {
  return {
    graph: state.graph
  }
}

export default connect(mapStateToProps, { updateFilterPeriod })(TimeRangeSelector);
