import React, { Component } from "react"
import { connect } from 'react-redux';
import {
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  Typography,
} from "@material-ui/core"

import { Error as ErrorIcon, Close as CloseIcon } from "@material-ui/icons"
import { hideAssortmentAlerts } from 'actions/graph'
import { getGranularity } from 'utils/map_filter_type_display'
import moment from 'moment'
import "./AssortmentDetailsDialog.scss"
import { alertList } from 'utils/alertList'
import { assortmentName } from 'utils/assortmentName'
import { varianceExceedsThreshhold, displayVariance, getVariance } from 'utils/getVariance'

class AssortmentDetailsDialog extends Component {
  getPointName(time) {
    if (getGranularity() === 'weekly') {
      return time.slice(4, 6)
    } else {
      return moment(time.slice(4, 6), 'MM').format('MMMM')
    }
  }

  getSellOutAlertText(data) {
    if (!data.sell_out_units_ytg || !data.sell_out_units_ytg.KPI) {
      return ''
    }
    if (!data.sell_out_units_sas || !data.sell_out_units_sas.KPI) {
      return ''
    }
    if (!varianceExceedsThreshhold(data.sell_out_units_ytg.KPI, data.sell_out_units_sas.KPI)) {
      return ''
    }
    if (getVariance(data.sell_out_units_ytg.KPI, data.sell_out_units_sas.KPI) > 0) {
      return "Sell-Out Units Prediction exceeds Sell-Out Plan By : " + displayVariance(data.sell_out_units_ytg.KPI, data.sell_out_units_sas.KPI) + '%';
    } else {
      return "Sell-Out Units Prediction is less than Sell-Out Plan By : " + displayVariance(data.sell_out_units_ytg.KPI, data.sell_out_units_sas.KPI) + '%';
    }
  }

  getDemandAlertText(data) {
    if (!data.demand_units_ytg || !data.demand_units_ytg.KPI) {
      return ''
    }
    if (!data.demand_units_sas || !data.demand_units_sas.KPI) {
      return ''
    }

    if (!varianceExceedsThreshhold(data.demand_units_ytg.KPI, data.demand_units_sas.KPI)) {
      return ''
    }
      // return "Demand Variance is: " + displayVariance(data.demand_units_ytg, data.demand_units_sas) + '%';
    if (getVariance(data.demand_units_ytg.KPI, data.demand_units_sas.KPI) > 0) {
      return "Demand Prediction exceeds Demand Plan By : " + displayVariance(data.demand_units_ytg.KPI, data.demand_units_sas.KPI) + '%';
    } else {
      return "Demand Prediction is less than Demand Plan By : " + displayVariance(data.demand_units_ytg.KPI, data.demand_units_sas.KPI) + '%';
    }
  }

  assortmentNotifications(assortment) {
    return (
      <ul className="paper notifications simple">
        <Typography variant="h3">
          <ErrorIcon />
          Alerts
        </Typography>
        {alertList(assortment) && alertList(assortment).length ? (
          alertList(assortment).map((notification, index) => {
            return (
              <li key={index}>
                <div>
                  Year {notification.date.slice(0, 4)} - {getGranularity() === 'weekly' ? 'Week' : 'Month'} {this.getPointName(notification.date)}
                  <Typography>
                    {this.getSellOutAlertText(notification)}
                  </Typography>
                  <Typography>
                    {this.getDemandAlertText(notification)}
                  </Typography>
                </div>
              </li>
            )
          })
        ) : (
            <li>
              <Typography>No alerts</Typography>
            </li>
          )}
      </ul>
    )
  }

  handleClose() {
    this.props.hideAssortmentAlerts()
  }

  render() {

    const assortment = (this.props.selectedAssortment && this.props.selectedAssortment[1]) || {}

    return (
      <Dialog
        aria-labelledby="dialog-title"
        open={this.props.showProductAlert}
        className="assortment-details-dialog category-title dialog alert-dialog-modal"
        maxWidth="md"
      >
        {this.props.showProductAlert && <DialogTitle id="dialog-title">
          <div className="dialog-sec merchandise-val">
            {/* <img
              width="48px"
              alt={assortment.detail.name}
              src={`https://lsco.scene7.com/is/image/lsco/levis/clothing/${assortment.detail.image_id ||
                assortment.detail.id}-front-pdp.jpg?$grid_desktop_bottoms$`}
            /> */}
            {assortmentName(this.props.selectedAssortment[0])}
          </div>
          <IconButton size="small" onClick={() => this.handleClose()}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>}

        {this.props.showProductAlert && <DialogContent>
          <div className="">
            {this.assortmentNotifications(assortment)}
          </div>
        </DialogContent>}
      </Dialog>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    selectedAssortment: Object.entries(state.product.products).find(([key, product]) => product.selected),
    showProductAlert: state.product.showProductAlert
  }
}

export default connect(mapStateToProps, { hideAssortmentAlerts })(AssortmentDetailsDialog);
