const getUpdatedDataForProduct = (state, dispatch, assortment, displayName) => {
  const initialYear = state.graph.graphDomain[0].slice(0, 4)
  const endYear = state.graph.graphDomain[1].slice(0, 4)
  if (initialYear === moment().subtract(1, 'year').format('YYYY')) {
    getDataForProduct(initialYear, state, dispatch, assortment, displayName)
  }
  if (endYear === moment().format('YYYY')) {
    getDataForProduct(endYear, state, dispatch, assortment, displayName)
  }
}

const getData = (year, granularity, state, dispatch) => {
  state.graph.savedProducts.forEach((product) => {
    getDataForProduct(year, state, dispatch, product.product, product.displayName)
  })
}

const getDataForProduct = (year, state, dispatch, product, displayName) => {
  URLS.forEach(value => {
    if ((String(year) === String(2019) || getGranularity() === 'weekly') && value.url.includes('SAS')) {
      return null
    }
    startRequest(dispatch, state)
    axios.get(value.url, { params: { year: value.last_year ? year - 1 : year, time_granularity: getGranularity(), ...JSON.parse(product) } }).then(response => {
      dispatch({
        type: UPDATE_CALENDAR_DATA,
        payload: {
          data: response.data
        }
      })
      dispatch({
        type: UPDATE_DATA,
        payload: {
          data: response.data,
          helper: value.helper,
          product: product,
          year: year,
          granularity: getDataKey()
        }
      })
      endRequest(dispatch)
    })
  })
}

export const getMerchData = (locationData) => {
  return (dispatch, getState) => {
    const state = getState()
    startRequest(dispatch, state)
    axios.get(`${HOST}/Master_Static_Info/merchandise-hierarchy-structure/`, { params: locationData }).then(response => {
      dispatch({
        type: UPDATE_MERCH_DATA,
        payload: {
          data: response.data
        }
      })
      endRequest(dispatch)
    })
  }
}

const getRegionData = (state, dispatch) => {
  startRequest(dispatch, state)
  axios.get(`${HOST}/Master_Static_Info/location-hierarchy-structure/`).then(response => {
    dispatch({
      type: UPDATE_REGION_DATA,
      payload: {
        data: response.data
      }
    })
    endRequest(dispatch)
  })
}

const getCalendarData = (state, dispatch) => {
  startRequest(dispatch, state)
  axios.get(`${HOST}/KPI/demand-units-LY/`, { params: { year: 2019, time_granularity: 'weekly', region: 'LSA', brand: 'LEVIS' } }).then(response => {
    dispatch({
      type: UPDATE_CALENDAR_DATA,
      payload: {
        data: response.data
      }
    })
    endRequest(dispatch)
  })
}

