import moment from 'moment'

export const GetYearIndex = (year) => {
  if(year === "2018") {
    return 1000
  } else {
    return moment().isSame(moment(year, 'YYYY'), 'year') ? (Number(year + '00') - 52) : Number(year + '00')
  }
}

export const GetMonthIndex = (year) => {
  if (year === "2018") {
    return 1000
  } else {
    return moment().isSame(moment(year, 'YYYY'), 'year') ? (Number(year + '00') - 12) : Number(year + '00')
  }
}
