import React, { Component } from "react";
import { connect } from 'react-redux';
import { getGranularity } from 'utils/map_filter_type_display'
import { GetYearIndex, GetMonthIndex } from 'utils/getYearIndex'
import ToggleAlert from 'components/dashboard/assortmentHelper/toggleAlert'
import { getWeekName, getSecondAxisNameWithYear } from 'utils/getTimeLineHelper'

import {
  CartesianGrid,
  LineChart,
  ResponsiveContainer,
  XAxis,
  YAxis,
  ReferenceLine
} from "recharts";

class Timeline extends Component {
  customizedTick = ({ x, y, stroke, payload }) => {
    return (
      <g transform={`translate(${x},${y})`}>
        <text x={70} y={0} dy={20} fontSize={'12px'} textAnchor="end">{getSecondAxisNameWithYear(payload.value)}</text>
      </g>
    )
  }

  customizedWeekTick = ({ x, y, stroke, payload }) => {
    return (
      <g transform={`translate(${x},${y})`}>
        <text x={40} y={0} dy={20} fontSize={'12px'} textAnchor="end">{getWeekName(payload.value)}</text>
      </g>
    )
  }

  getMaxWeek(dataMax) {
    if (getGranularity() === 'weekly') {
      return this.props.graph.graphDomain[1] - GetYearIndex(this.props.graph.graphDomain[1].slice(0, 4))
    } else {
      return this.props.graph.graphDomain[1] - GetMonthIndex(this.props.graph.graphDomain[1].slice(0, 4))
    }
  }

  getMinWeek(dataMin) {
    if (getGranularity() === 'weekly') {
      return this.props.graph.graphDomain[0] - GetYearIndex(this.props.graph.graphDomain[0].slice(0, 4))
    } else {
      return this.props.graph.graphDomain[0] - GetMonthIndex(this.props.graph.graphDomain[0].slice(0, 4))
    }
  }

  getTickCount() {
    if (getGranularity() === 'weekly') {
      return (this.props.graph.graphDomain[1] - GetYearIndex(this.props.graph.graphDomain[1].slice(0, 4))) - (this.props.graph.graphDomain[0] - GetYearIndex(this.props.graph.graphDomain[0].slice(0, 4)) - 1)
    } else {
      return (this.props.graph.graphDomain[1] - GetMonthIndex(this.props.graph.graphDomain[1].slice(0, 4))) - (this.props.graph.graphDomain[0] - GetMonthIndex(this.props.graph.graphDomain[0].slice(0, 4)) - 1)
    }
  }

  render() {
    const { data, theme, now } = this.props;


    return (
      <>
        <li className="legend year chart-head" key="legend-year">
          <span className="alert-sec">
            <ToggleAlert></ToggleAlert>
          </span>
          <ResponsiveContainer className="graph">
            <LineChart
              data={data}
              margin={{
                top: 0,
                right: 45,
                left: 66,
                bottom: 0
              }}
            >
              <XAxis
                dataKey="g_index"
                type="number"
                domain={[dataMin => { return this.getMinWeek(dataMin) }, dataMax => { return this.getMaxWeek(dataMax) }]}
                tickFormatter={(val) => getSecondAxisNameWithYear(val)}
                tickCount={this.getTickCount()}
                orientation={'top'}
                tickLine={true}
                hide={false}
                fontSize={'12px'}
                mirror={false}
                dx={20}
                stroke='#d4d4d4'
                tick={(tickData) => this.customizedTick(tickData)}
                tickSize={20}
              />

              <YAxis hide={true} />
              <CartesianGrid horizontal={true} />
              <ReferenceLine
                x={now}
                stroke={theme.colorRef}
                strokeWidth={theme.strokeWidth}
              />
            </LineChart>
          </ResponsiveContainer>
        </li>
        <li className="legend month" key="legend-month">
          <ResponsiveContainer className="graph">
            <LineChart
              data={data}
              margin={{
                top: 0,
                right: 45,
                left: 50,
                bottom: 0
              }}
            >

              <XAxis
                dataKey="g_index"
                type="number"
                domain={[dataMin => { return this.getMinWeek(dataMin) }, dataMax => { return this.getMaxWeek(dataMax) }]}
                tickFormatter={val => getWeekName(val)}
                tickCount={this.getTickCount()}
                orientation={'top'}
                tick={(tickData) => this.customizedWeekTick(tickData)}
                tickLine={false}
                hide={false}
                fontSize={'12px'}
                dx={20}
                tickSize={0}
              />

              <YAxis hide={true} />
              <CartesianGrid horizontal={false} />
              <ReferenceLine
                x={now}
                stroke={theme.colorRef}
                strokeWidth={theme.strokeWidth}
              />
            </LineChart>
          </ResponsiveContainer>
        </li>
      </>
    );
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    graph: state.graph
  }
}

export default connect(mapStateToProps, { })(Timeline);
