import React, { Component } from "react"
import { connect } from 'react-redux';
import { Typography, Tooltip } from "@material-ui/core"
import HRNumbers from 'human-readable-numbers'
import { displayABSVariance, varianceClass } from 'utils/getVariance'

class GlobalData extends Component {
  render() {

    const product = (this.props.product && this.props.product.products[this.props.graph.selectedProductId]) ? this.props.product.products[this.props.graph.selectedProductId]['total'][this.props.graph.graphDomain[0].slice(0, 4)] : {}
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])

    return (
      <li className={"bg-" + this.props.graph.graphDomain[0].slice(0, 4) + " paper textured yearly total-yearly-val"}>
        <ul className="body variance-val">
          <li>
            <Typography variant="h5">Sell-Out Units</Typography>
            <Tooltip title={isProductAvailable ? 'Sum of YTD actuals + prediction for selected year (where available)' : 'Please select a product first'}>
              <Typography variant="h2">
                {HRNumbers.toHumanString(product['sell_out_units_ytd'] || 0)}
              </Typography>
            </Tooltip>
          </li>
          <li>
            <Typography variant="h5">Demand</Typography>
            <Tooltip title={isProductAvailable ? 'Sum of adjusted demand YTD actuals + PDU prediction for selected year' : 'Please select a product first'}>
              <Typography variant="h2">
                {HRNumbers.toHumanString(product['demand_units_ytg'] || 0)}
              </Typography>
            </Tooltip>
          </li>
          <li>
            <Typography variant="h5">Sell-Out Units LY</Typography>
            <Tooltip title={isProductAvailable ? 'Sum of LY actuals for selected year (where available)' : 'Please select a product first'}>
              <Typography variant="h4">
                {HRNumbers.toHumanString(product['sell_out_units_ly'] || 0)}
              </Typography>
            </Tooltip>
          </li>
          <li>
            <Typography variant="h5">Demand LY</Typography>
            <Tooltip title={isProductAvailable ? 'Sum of adjusted demand LY actuals for selected year' : 'Please select a product first'}>
              <Typography variant="h4">
                {HRNumbers.toHumanString(product['demand_units_ly'] || 0)}
              </Typography>
            </Tooltip>
          </li>
          <li>
            <Typography variant="h5">%Variance</Typography>
            <Typography variant="h4" className={varianceClass(product['sell_out_units_ytd'], product['sell_out_units_ly']) + ' p-l-10'}>
              {displayABSVariance(product['sell_out_units_ytd'], product['sell_out_units_ly'])}%
            </Typography>
          </li>

          <li>
            <Typography variant="h5">%Variance</Typography>
            <Typography variant="h4" className={varianceClass(product['demand_units_ytg'], product['demand_units_ly']) + ' p-l-10'}>
              {displayABSVariance(product['demand_units_ytg'], product['demand_units_ly'])}%
            </Typography>
          </li>
        </ul>
      </li>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(GlobalData);

