import React, { Component } from "react"
import { connect } from 'react-redux';
import { Typography} from "@material-ui/core"
import HRNumbers from 'human-readable-numbers'
import {
  BarChart,
  Bar,
  LabelList,
  YAxis,
  CartesianGrid
} from "recharts"

class YearlyComparison extends Component {

  demandData() {
    const product = (this.props.product && this.props.product.products[this.props.graph.selectedProductId]) ? this.props.product.products[this.props.graph.selectedProductId]['total'][this.props.graph.graphDomain[0].slice(0, 4)] : {}
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])
    if (isProductAvailable && product && product.demand_units_ly && (product.demand_units_ytd)) {
      return [
        { name: '2019', demand: product.demand_units_ly, sellout: (product.demand_units_ytd), final: Math.abs(product.demand_units_ly - (product.demand_units_ytd)) }
      ]
    }
  }

  sellOutData() {
    const product = (this.props.product && this.props.product.products[this.props.graph.selectedProductId]) ? this.props.product.products[this.props.graph.selectedProductId]['total'][this.props.graph.graphDomain[0].slice(0, 4)] : {}
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])
    if (isProductAvailable && product && product.sell_out_units_ly && (product.sell_out_units_ytd)) {
      return [
        {
          name: '2020',
          demand: product.sell_out_units_ly,
          sellout: (product.sell_out_units_ytd),
          final: Math.abs(product.sell_out_units_ly - (product.sell_out_units_ytd))
        }
      ]
    }
  }

  render() {
    const product = (this.props.product && this.props.product.products[this.props.graph.selectedProductId]) ? this.props.product.products[this.props.graph.selectedProductId]['total'][this.props.graph.graphDomain[0].slice(0, 4)] : {}

    return (
      <li className="comparison-sec paper">
        <div className="d-flex justify-center m-b-9">
          <Typography variant="h4" className="">{this.props.graph.graphDomain[0].slice(0, 4).toString() - 1} vs {this.props.graph.graphDomain[0].slice(0, 4)} YEARLY COMPARISON
</Typography>
          <ul className="m-l-15 d-flex">
            <li className="">{this.props.graph.graphDomain[0].slice(0, 4).toString() - 1}</li>
            <li className="">{this.props.graph.graphDomain[0].slice(0, 4)}</li>
          </ul>
        </div>
        <div className="d-flex">
          <div className="">
            <BarChart barSize={40} width={200} height={165} data={this.sellOutData()}
              margin={{ top: 5, right: 0, left: 20, bottom: 5 }}>
              <CartesianGrid vertical={false} />
              <YAxis
                mirror
                orientation='right'
                fontSize={'10px'}
                dy={7}
                tickFormatter={(val) => HRNumbers.toHumanString(val)}
                tickLine={false}
              />
              <Bar isAnimationActive={false} dataKey="demand" fill="#ea9191" />
              <Bar isAnimationActive={false} dataKey="sellout" fill="#79ade8" />
              <Bar isAnimationActive={false} barSize={25} dataKey="sellout" fill="transparent" />
              {product.sell_out_units_ly > (product.sell_out_units_ytd) && <Bar isAnimationActive={false} barSize={2} stackId='sellout' dataKey="sellout" fill="transparent" />}
              {product.sell_out_units_ly <= (product.sell_out_units_ytd) && <Bar isAnimationActive={false} barSize={2} stackId='sellout' dataKey="demand" fill="transparent" />}
              <Bar isAnimationActive={false} barSize={2} stackId='sellout' dataKey="final" fill={ product.sell_out_units_ly > (product.sell_out_units_ytd) ? 'red' : 'green' } >
                {product.sell_out_units_ly > (product.sell_out_units_ytd) && <LabelList x={143} dataKey="final" y={String(((165 / product.sell_out_units_ly) * (Math.abs(product.sell_out_units_ly - (product.sell_out_units_ytd)) / 2)))} fill={product.sell_out_units_ly > (product.sell_out_units_ytd) ? 'red' : 'green'} fontSize={'10px'} position="left" formatter={(val) => HRNumbers.toHumanString(val)} />}
                {product.sell_out_units_ly <= (product.sell_out_units_ytd) && <LabelList x={143} dataKey="final" y={String(((165 / (product.sell_out_units_ytd)) * (Math.abs(product.sell_out_units_ytd*10 - (product.sell_out_units_ly)) / 2)))} fill={product.sell_out_units_ly > (product.sell_out_units_ytd) ? 'red' : 'green'} fontSize={'10px'} position="left" formatter={(val) => HRNumbers.toHumanString(val)} />}
              </Bar>
              <Bar isAnimationActive={false} dataKey="sellout" fill="transparent" />
            </BarChart>
            <div className="yearly-heading">
              SELL-OUT
            </div>
          </div>
          <div>
            <BarChart barSize={40} width={220} height={165} data={this.demandData()}
              margin={{ top: 5, right: 30, left: 0, bottom: 5 }}>
              <CartesianGrid vertical={false} />
              <YAxis
                mirror
                orientation='left'
                fontSize={'10px'}
                dy={7}
                tickLine={false}
                tickFormatter={(val) => HRNumbers.toHumanString(val)}
              />
              <Bar isAnimationActive={false} dataKey="sellout" fill="transparent" />
              {product.demand_units_ly > ((product.demand_units_ytd)) && <Bar isAnimationActive={false} barSize={2} stackId='demand' dataKey="sellout" fill="transparent" />}
              {product.demand_units_ly <= ((product.demand_units_ytd)) && <Bar isAnimationActive={false} barSize={2} stackId='demand' dataKey="demand" fill="transparent" />}
              <Bar isAnimationActive={false} barSize={2} stackId='demand' dataKey="final" fill={product.demand_units_ly > (product.demand_units_ytd) ? 'red' : 'green'} >
                {product.demand_units_ly > ((product.demand_units_ytd)) && <LabelList x={63} dataKey="final" y={String(((165 / product.demand_units_ly) * (Math.abs(product.demand_units_ly - (product.demand_units_ytd)) / 2)))} fill={product.demand_units_ly > ((product.demand_units_ytd)) ? 'red' : 'green'} fontSize={'10px'} position="right" formatter={(val) => HRNumbers.toHumanString(val)} />}
                {product.demand_units_ly <= ((product.demand_units_ytd)) && <LabelList x={63} dataKey="final" y={String(((165 / (product.demand_units_ytd)) * (Math.abs((product.demand_units_ytd) - product.demand_units_ly) / 2)))} fill={product.demand_units_ly > ((product.demand_units_ytd)) ? 'red' : 'green'} fontSize={'10px'} position="right" formatter={(val) => HRNumbers.toHumanString(val)} />}
              </Bar>
              <Bar isAnimationActive={false} barSize={25} dataKey="sellout" fill="transparent" />
              <Bar isAnimationActive={false} dataKey="demand" fill="#ea9191" />
              <Bar isAnimationActive={false} dataKey="sellout" fill="#79ade8" />
            </BarChart>
            <div className="yearly-heading demand-head">
              Demand
            </div>
          </div>
        </div>

      </li>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(YearlyComparison);

