import React, { Component } from "react"
import PropTypes from "prop-types"
import { connect } from 'react-redux';
import { getMerchData, addProduct } from 'actions/loadGraphData'
import { showFlashMessage } from 'actions/home'

import {
  Box,
  IconButton,
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
  Typography,
  Input,
} from "@material-ui/core"

import TreeItem from '@material-ui/lab/TreeItem';
import TreeView from '@material-ui/lab/TreeView';

import {
  AddCircle as AddCircleIcon
} from "@material-ui/icons"

import CheckBoxOutlineBlankRoundedIcon from '@material-ui/icons/CheckBoxOutlineBlankRounded';
import { CheckBoxChecked } from 'components/shared/icons/checkbox_checked'
import { CheckBoxIcon}  from '@material-ui/icons/CheckBox';
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded';

import "./AssortmentBrowser.scss"

class AssortmentBrowser extends Component {

  constructor(props) {
    super(props)
    this.state = {
      expanded: [],
      node: {},
      productResult: [],
      result: [],
      value: 0,
      productBreadcrumb: [],
      breadcrumb: [],
      step: 0,
      regionJson: {}
    }
  }

  handleClose() {
    this.setState({ productResult: [], step: 0, expanded: [], productBreadcrumb: [], breadcrumb: [], node: {} })
    this.props.onClose()
  }

  handleChange = (event, nodes) => {
    this.setState({ expanded: nodes })
  };

  handleOnSelection = (assortment, event, node) => {
    this.setState({ regionJson: assortment, step: 1, productResult: [], expanded: [], productBreadcrumb: [], breadcrumb: [], node: node })
    this.props.getMerchData(assortment)
    event.stopPropagation()
  }

  handleOnMerchSelection = (assortment, event, displayName) => {
    this.props.addProduct({ assortment: { ...this.state.regionJson, ...assortment }, locationDisplayName: this.state.node.displayName, displayName: displayName })
    this.setState({ regionJson: {}, step: 0 })
    this.props.showFlashMessage('Assortment successfully added', 'success')
    this.handleClose()
    event.stopPropagation()
  }

  treeViewLabel = (node) => {
    return (
      <div key={node.id}>
        {node.name}
        <IconButton
          edge="end"
          aria-label="add"
          onClick={(e) => this.handleOnSelection(node.uniqueId, e, node)}
        >
          { node.displayName === this.state.node.displayName && <CheckBoxIcon size={'24'} color1={'#008000'} color2={'#0C580C'} index={20} /> }
          { node.displayName !== this.state.node.displayName && <CheckBoxOutlineBlankRoundedIcon className="check-product" /> }
        </IconButton>
      </div>
    )
  }

  treeViewMerchLabel = (consumerVal) => {
    return (
      <div key={consumerVal.id}>
        {consumerVal.name}
        <IconButton
          edge="end"
          aria-label="add"
          onClick={(e) => this.handleOnMerchSelection(consumerVal.uniqueId, e, consumerVal.displayName)}
        >
          <CheckBoxOutlineBlankRoundedIcon className="check-product" />
        </IconButton>
      </div>
    )
  }

  getObjects = (obj, key, val) => {
    var objects = [];
    for (var i in obj) {
      if (!obj.hasOwnProperty(i)) continue;
      if (typeof obj[i] === 'object') {
        objects = objects.concat(this.getObjects(obj[i], key, val));
      } else
        //if key matches and value matches or if key matches and value is not passed (eliminating the case where key matches but passed value does not)
        if ((i === key && obj[i].toLocaleLowerCase().includes(val.toLocaleLowerCase())) || (i === key && val === '')) { //
          objects.push(obj);
        } else if (obj[i] === val && key === '') {
          //only add if the object is not already in the array
          if (objects.lastIndexOf(obj) === -1) {
            objects.push(obj);
          }
        }
    }
    return objects;
  }

  handleProductBreadCrumbChange = (node) => {
    if (node.breadcrumb) {
      this.setState({ productBreadcrumb: node.breadcrumb })
    }
  };

  findProductCategories = (e, term) => {
    const SearchObject = (this.state.step === 0) ? this.props.home.regionDetails : this.props.home.merchDetails
    if (e.target.value) {
      this.setState({ productResult: this.getObjects(SearchObject, 'name', e.target.value) })
    } else {
      this.setState({ productResult: [] })
    }
  }

  findProducts = (e, term) => {
    if (e.target.value) {
      this.setState({ productResult: this.getObjects(this.props.home.regionDetails, 'name', e.target.value) })
    } else {
      this.setState({ productResult: [] })
    }
  }

  gotoSelectedProductBreadCrumb = (breadcrumb) => {
    if (document.querySelector(`#${breadcrumb.id}`)) {
      document.querySelector(`#${breadcrumb.id}`).scrollIntoView()
    }
  }

  render() {

    const classes = {}

    const { expanded, productResult, step } = this.state;

    const { home } = this.props

    return (
    <div className="sidebar-product">
      <div className="tab-block">
        <span className={(step === 0) ? "active" : ''} onClick={(e) => this.setState({ step: 0 })}>
          Location
        </span>
        <span className={(step === 1) ? "active" : ''}>Merchandise</span>
      </div>

      <div>
        {<>
          <Input disableUnderline={true} placeholder="Search" className={classes.inputTypeSearch} variant="outlined" onChange={(e, term) => this.findProductCategories(e, term)}></Input>
          <List component="div" disablePadding className="search-result">
            {productResult.map((result, r_index) => {
              return (
                <div key={result.key}>
                { result.displayName && <ListItem key={result.key} className={classes.nested}>
                  <ListItemText primary={result.displayName} />
                  <ListItemSecondaryAction>
                    <IconButton
                      edge="end"
                      aria-label="add"
                        onClick={(e) => (step === 0) ? this.handleOnSelection(result.uniqueId, e, result) : this.handleOnMerchSelection(result.uniqueId, e, result.displayName)}
                    >
                      <CheckBoxOutlineBlankRoundedIcon className="check-product" />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem> }
                </div>
              )
            })}
          </List>
        </>}

        <div className="breadcrumb-result">
          {
            this.state.productBreadcrumb.map((breadcrumb) => {
              return (<div className="result-val" key={breadcrumb.id} onClick={(e) => this.gotoSelectedProductBreadCrumb(breadcrumb)}>
                {breadcrumb.name}
              </div>)
            })
          }
        </div>

        <List className="merchandise-list" component="div" disablePadding>
          {(step === 0) && <TreeView
            className={classes.cat_root}
            defaultCollapseIcon={<RemoveCircleRoundedIcon />}
            defaultExpandIcon={<AddCircleIcon/>}
            expanded={expanded}
            onNodeToggle={this.handleChange}
          >
            {home.regionDetails.region && home.regionDetails.region.map((region, b_index) => {
              return (
                <TreeItem onClick={(e) => this.handleProductBreadCrumbChange(region, e)} id={region.nodeId} key={region.nodeId} nodeId={region.nodeId} label={this.treeViewLabel(region)}>
                  {region.affiliate.map((affiliate, p_index) => {
                    return (
                      <TreeItem onClick={(e) => this.handleProductBreadCrumbChange(affiliate, e)} id={affiliate.nodeId} key={affiliate.nodeId} nodeId={affiliate.nodeId} label={this.treeViewLabel(affiliate)}>
                        {affiliate.channels.map((channel, sc_index) => {
                          return (
                            <TreeItem onClick={(e) => this.handleProductBreadCrumbChange(channel, e)} id={channel.nodeId} key={channel.nodeId} nodeId={channel.nodeId} label={this.treeViewLabel(channel)}>
                              {channel.planning_groups.map((planning_group, pr_index) => {
                                return (<TreeItem onClick={(e) => this.handleProductBreadCrumbChange(planning_group, e)} id={planning_group.nodeId} key={planning_group.nodeId} nodeId={planning_group.nodeId} label={this.treeViewLabel(planning_group)}>
                                </TreeItem>)
                              })}
                            </TreeItem>
                          )
                        })}
                      </TreeItem>
                    )
                  })}
                </TreeItem>
              )
            })}
          </TreeView>}
          {(step === 1) && <div>
            <TreeView
              className={classes.cat_root}
              defaultCollapseIcon={<RemoveCircleRoundedIcon />}
              defaultExpandIcon={<AddCircleIcon />}
              expanded={expanded}
              onNodeToggle={this.handleChange}
            >
              {home.merchDetails.Brand && home.merchDetails.Brand.map((brandVal, b_index) => {
                return (
                  <TreeItem id={brandVal.nodeId} onClick={(e) => this.handleProductBreadCrumbChange(brandVal, e)} nodeId={brandVal.nodeId} key={brandVal.nodeId} label={this.treeViewMerchLabel(brandVal)}>
                    {brandVal.Consumer.map((consumerVal, p_index) => {
                      return (
                        <TreeItem id={consumerVal.nodeId} onClick={(e) => this.handleProductBreadCrumbChange(consumerVal, e)} nodeId={consumerVal.nodeId} key={consumerVal.nodeId} label={this.treeViewMerchLabel(consumerVal)}>
                          {consumerVal.Category.map((categoryVal, sc_index) => {
                            return (
                              <TreeItem id={categoryVal.nodeId} onClick={(e) => this.handleProductBreadCrumbChange(categoryVal, e)} nodeId={categoryVal.nodeId} key={categoryVal.nodeId} label={this.treeViewMerchLabel(categoryVal)}>
                              </TreeItem>
                            )
                          })}
                        </TreeItem>
                      )
                    })}
                  </TreeItem>
                )
              })}
            </TreeView>
          </div>
          }
        </List>
      </div>
    </div>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    home: state.home
  }
}

export default connect(mapStateToProps, { getMerchData, addProduct, showFlashMessage })(AssortmentBrowser);

function TabPanel(props) {
  const { children, value, index, ...other } = props

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  )
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
}
