import React, { Component } from "react"
import { Route, Switch, BrowserRouter, Redirect } from "react-router-dom"
import Dashboard from "components/dashboard/Dashboard"
import Login from "components/login/Login.js"
import { connect } from 'react-redux';
import { showAssortBrowser } from 'actions/home';

class Router extends Component {

  componentDidMount() {
    if (!this.props.graph.savedProducts.length) {
      this.props.showAssortBrowser()
    }
  }

  render() {

    const loggedIn = this.props.auth.loggedIn

    return (
      <BrowserRouter>
        <Switch>
          { !loggedIn && <Route path="/" exact component={Login} /> }
          {!loggedIn && <Redirect to="/" exact from='/dashboard' />}
          {loggedIn && <Redirect to="/dashboard" exact from='/' /> }
          { loggedIn && <Route path="/dashboard" component={Dashboard} /> }
        </Switch>
      </BrowserRouter>
    )
  }
}


const mapStateToProps = (state, _ownProps) => {
  return {
    auth: state.auth,
    graph: state.graph
  }
}

export default connect(mapStateToProps, { showAssortBrowser })(Router);
