import moment from 'moment'
import { GetYearIndex, GetMonthIndex} from 'utils/getYearIndex'
import { getGranularity } from 'utils/map_filter_type_display'
import { productDataForTime } from 'utils/datumForTime'

export const getSeasonName = (val) => {
  if (getFiscalNumber(val) === 1) {
    return 'H1'
  } else if (getFiscalNumber(val) === 7) {
    return 'H2'
  } else {
    return ''
  }
}

export const getFirstQuarterName = (val) => {
  if (getFiscalNumber(val) === 12 || getFiscalNumber(val) === 1) {
    return 'Q1'
  } else if (getFiscalNumber(val) === 4) {
    return 'Q2'
  } else if (getFiscalNumber(val) === 7) {
    return 'Q3'
  } else if (getFiscalNumber(val) === 10) {
    return 'Q4'
  } else {
    return ''
  }
}

export const getSecondQuarterName = (val) => {
  if (getFiscalNumber(val) === 3) {
    return 'Q1'
  } else if (getFiscalNumber(val) === 6) {
    return 'Q2'
  } else if (getFiscalNumber(val) === 9) {
    return 'Q3'
  } else if (getFiscalNumber(val) === 12) {
    return 'Q4'
  } else {
    return ''
  }
}

export const getFirstMonthName = (val) => {
  const fiscalNumber = getFiscalNumber(val)
  var fiscalWeek = fiscalNumber > 52 ? fiscalNumber - 52 : fiscalNumber
  const momentWeek = moment(fiscalWeek, 'WW')
  if ((momentWeek.weeks() - moment(fiscalWeek, 'WW').add(0, 'month').startOf('month').weeks() + 1) === 2) {
    return momentWeek.add(1, 'months').format('MMM')
  } else {
    return ''
  }
}

export const getSecondMonthName = (val) => {
  const fiscalNumber = getFiscalNumber(val)
  var fiscalWeek = fiscalNumber > 52 ? fiscalNumber - 52 : fiscalNumber
  const momentWeek = moment(fiscalWeek, 'WW')
  if ((momentWeek.weeks() - moment(fiscalWeek, 'WW').add(0, 'month').startOf('month').weeks() + 1) === 2) {
    return momentWeek.format('MMM')
  } else {
    return ''
  }
}


const getWeekName = (val) => {
  if (val > 52) {
    return `FW ${val - 52}`
  }
  return `FW ${val}`
}

const getYear = (graphDomain) => {
  return ' ' + graphDomain.slice(0, 4)
}

const getFiscalNumber = (val) => {
  var fiscal
  if (getGranularity() === 'weekly') {
    fiscal = val - GetYearIndex(val.slice(0, 4))
    return fiscal > 52 ? fiscal - 52 : fiscal
  } else {
    fiscal = val - GetMonthIndex(val.slice(0, 4))
    return fiscal > 12 ? fiscal - 12 : fiscal
  }
}

export const getTimeRange = (graphDomain, filterPeriod) => {
  switch (filterPeriod) {
    case 'year':
      return graphDomain[0].slice(0, 4)
    case 'season':
      return getSeasonName(graphDomain[0]) + getYear(graphDomain[0])
    case 'quarter':
      return getFirstQuarterName(graphDomain[0]) + getYear(graphDomain[0]) + ' - ' + getSecondQuarterName(graphDomain[1]) + getYear(graphDomain[1])
    case 'month':
      return (productDataForTime(graphDomain[0]) && productDataForTime(graphDomain[1])) ? (productDataForTime(graphDomain[0]).fisc_month_name + ' ' + String(productDataForTime(graphDomain[0]).fisc_month).slice(0, 4) + ' - ' + productDataForTime(graphDomain[1]).fisc_month_name + ' ' + String(productDataForTime(graphDomain[1]).fisc_month).slice(0, 4)) : ''
    case 'week':
      return 'FW' + graphDomain[0].slice(4, 6) + getYear(graphDomain[0]) + ' - FW' + graphDomain[1].slice(4, 6) + getYear(graphDomain[1])
    default:
      break;
  }
}
