import Authentication from 'data/authentication'
import Home from 'data/home'
import Shared from 'data/shared'
import Graph from 'data/graph'
import Product from 'data/product';

export default combineReducers({
  auth: Authentication,
  home: Home,
  graph: Graph,
  shared: Shared,
  product: Product
})
