import React, { Component } from "react"
import { connect } from 'react-redux';
import { Snackbar, IconButton } from "@material-ui/core";
import { Alert as MuiAlert } from "@material-ui/lab"
import { CloseRounded as CloseRoundedIcon } from "@material-ui/icons"
import { hideFlashMessage } from 'actions/home';

class FlashMessage extends Component {
  render() {

    const { alertOpen, alertMessage, severity } = this.props.shared.flash;

    return (
      <Snackbar
        open={alertOpen}
        autoHideDuration={2000}
        onClose={() => this.props.hideFlashMessage()}
      >
        <MuiAlert
          elevation={6}
          variant="filled"
          action={
            <IconButton color="inherit" size="small">
              <CloseRoundedIcon />
            </IconButton>
          }
          onClose={() => this.props.hideFlashMessage()}
          severity={severity}
        >
          {alertMessage}
        </MuiAlert>
      </Snackbar>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    shared: state.shared
  }
}

export default connect(mapStateToProps, { hideFlashMessage })(FlashMessage);
