import { SHOW_ASSORT_BROWSER, HIDE_ASSORT_BROWSER, SHOW_FLASH_MESSAGE, HIDE_FLASH_MESSAGE, SHOW_CONFIRM_BOX, HIDE_CONFIRM_BOX, SHOW_LOADER, HIDE_LOADER } from 'utils/types'

export const showConfirmBox = (subject, body) => {
  return {
    type: SHOW_CONFIRM_BOX,
    payload: { body: body, subject: subject }
  }
}
export const hideConfirmBox = () => {
  return {
    type: HIDE_CONFIRM_BOX,
    payload: false
  }
}

export const showFlashMessage = (message, severity) => {
  return {
    type: SHOW_FLASH_MESSAGE,
    payload: { message: message, severity: severity }
  }
}

export const hideFlashMessage = () => {
  return {
    type: HIDE_FLASH_MESSAGE,
    payload: false
  }
}

export const showAssortBrowser = () => {
  return {
    type: SHOW_ASSORT_BROWSER,
    payload: true
  }
}

export const hideAssortBrowser = () => {
  return {
    type: HIDE_ASSORT_BROWSER,
    payload: false
  }
}

export const showLoader = () => {
  return {
    type: SHOW_LOADER,
    payload: false
  }
}

export const hideLoader = () => {
  return {
    type: HIDE_LOADER,
    payload: false
  }
}
