import { GraphHelper } from 'utils/graphHelper'
import { store } from 'utils/configureStore';

export const datumForTime = (data) => {
  let dataKPI = Object.keys(GraphHelper).find((kpi) => {
    return data[kpi]
  })
  return data[dataKPI]
}

export const productDataForTime = (date) => {
  let data = null
  Object.values(store.getState().product.products).forEach(product => {
    data = product.data.find((datum) => datum.date === date)
  })

  return data && datumForTime(data)
}
