import { store } from 'utils/configureStore';
import React from "react"

export const assortmentNameJSON = (assortment) => {
  const state = store.getState()
  const product = state.graph.savedProducts.find((pr) => pr.product === assortment)
  return JSON.parse(product.product)
}

export const assortmentName = (assortment) => {
  const state = store.getState()
  const product = state.graph.savedProducts.find((pr) => pr.product === assortment)
  const productName = JSON.parse(product.product)
  return (<>
    <div className="prod-region">
      <span className="heading">Location</span>
      <span className='bold'> {productName.region} </span>
      { productName.affiliate && <span> {productName.affiliate} </span> }
      { productName.channel && <span> {productName.channel} </span> }
      { productName.planning_group_name && <span> {productName.planning_group_name} </span> }
    </div>
    <div className="prod-brand">
      <span className="heading">Merchandise</span>
      {<span className='bold'> {productName.brand} </span>}
      {productName.consumer && <span> {productName.consumer} </span>}
      {productName.category && <span> {productName.category} </span>}
    </div>
  </>)
}

export const assortmentNameWithoutStyling = (assortment) => {
  const state = store.getState()
  const product = state.graph.savedProducts.find((pr) => pr.product === assortment)
  return product.displayName + '/ ' + product.locationDisplayName
}
