import React, { Component } from "react"
import { connect } from 'react-redux';
import { Checkbox, FormControlLabel, Grid } from "@material-ui/core"
import { toggleKPI } from 'actions/kpiHandler'
import { CheckBoxUnchecked } from 'components/shared/icons/checkbox_unchecked'
import { CheckBoxChecked } from 'components/shared/icons/checkbox_checked'

class KPISelector extends Component {
  render() {
    const { graph_kpis } = this.props;

    return (
      <Grid container spacing={1}>
        { Object.keys(graph_kpis).map((kpi, index) => {
          const kpiData = graph_kpis[kpi]
          return (
            <div className="p-r-15" key={kpi}>
              <FormControlLabel
                control={
                  <Checkbox color="default"
                    icon={<CheckBoxUnchecked color={kpiData.style.color} index={index} />}
                    checkedIcon={<CheckBoxChecked size={'32px'} color1={kpiData.style.color1} color2={kpiData.style.color2} index={index} />}
                    checked={kpiData.selected}
                    onChange={() => this.props.toggleKPI(kpi)}
                  />
                }
                label={ kpiData.name }
              />
            </div>
          )
        }) }
      </Grid>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    graph_kpis: state.graph.kpis
  }
}

export default connect(mapStateToProps, { toggleKPI })(KPISelector);

