import React, { Component } from "react"
import { connect } from 'react-redux';
import { Checkbox, FormControlLabel } from '@material-ui/core';
import { toggleAlert } from 'actions/graph'
import { CheckBoxUnchecked } from 'components/shared/icons/checkbox_unchecked'
import { CheckBoxChecked } from 'components/shared/icons/checkbox_checked'

class ToggleAlert extends Component {
  render() {
    return (
      <FormControlLabel
        control={
          <Checkbox
            checked={this.props.graph.showAlert}
            onChange={() => this.props.toggleAlert()}
            name="toggleAlert"
            icon={<CheckBoxUnchecked size={'14px'} color={'#FF0000'} index={354} />}
            checkedIcon={<CheckBoxChecked size={'14px'} color1={'#FF0000'} color2={'#740505'} index={354} />}
          />
        }
        label="Show Alerts"
      />
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    graph: state.graph
  }
}

export default connect(mapStateToProps, { toggleAlert })(ToggleAlert);
