import React, { Component } from "react"
import { Link } from "react-router-dom"
import { connect } from 'react-redux';
import { logOutUser } from 'actions/authentication/login'

import "./Navbar.scss"

class Navbar extends Component {
  render() {
    return (
      <div className="navbar shadow">
        <Link to="/dashboard" className="home">
          <img className="full-width" alt="home" src="/images/levis-logo.png" />
        </Link>
        <Link to="/dashboard" className="links dashboard active">
          <span>dashboard</span>
        </Link>
        <Link to="/" className='links exit' onClick={() => this.props.logOutUser()}>
          <span>exit</span>
        </Link>
      </div>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return { }
}

export default connect(mapStateToProps, { logOutUser })(Navbar);
