import React, { Component } from "react"
import { connect } from 'react-redux';
import { Typography, Tooltip } from "@material-ui/core"
import HRNumbers from 'human-readable-numbers'
import { getTimeRange } from 'utils/getTimeRange'
import MAP_FILTER_TYPE_DISPLAY from 'utils/map_filter_type_display'
import { displayABSVariance, varianceClass } from 'utils/getVariance'

class PDUData extends Component {

  splitData() {
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])
    if(isProductAvailable) {
      const product = this.props.product.products[this.props.graph.selectedProductId][MAP_FILTER_TYPE_DISPLAY[this.props.graph.filterPeriod]['data']]
      return product.filter((pr) => pr.date >= this.props.graph.graphDomain[0] && pr.date <= this.props.graph.graphDomain[1])
    }
    return [{}]
  }

  sell_out_units() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + (Number((data.sell_out_units_ytg && data.sell_out_units_ytg.KPI) || 0) || Number((data.sell_out_units_ytd && data.sell_out_units_ytd.KPI) || 0))
    })
    return sum
  }

  sell_out_units_ly() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + Number((data.sell_out_units_ly && data.sell_out_units_ly.KPI) || 0)
    })
    return sum
  }

  demand_units() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + (Number((data.demand_units_ytg && data.demand_units_ytg.KPI) || 0) || Number((data.demand_units_ytd && data.demand_units_ytd.KPI) || 0))
    })
    return sum
  }

  demand_units_ly() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + Number((data.demand_units_ly && data.demand_units_ly.KPI) || 0)
    })
    return sum
  }

  render() {

    const { graph } = this.props;
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])

    return (
      <ul className="kpis-section kpis-section-top">
        <li className="yearly paper col-2 graph-right text-center">
          <Typography variant="h4" className="m-b-0-hard text-center">Trend Plot KPI Summary</Typography>
          <Typography variant="h6" className="text-center">{getTimeRange(graph.graphDomain, graph.filterPeriod)}</Typography>
          <ul className="body variance-val">
            <li>
              <Typography variant="h5">Sell-Out Units</Typography>
              <Tooltip title={isProductAvailable ? 'Sum of YTD actuals + prediction for time selection (where available)' : 'Please select a product first' }>
                <Typography>
                  {HRNumbers.toHumanString(this.sell_out_units() || 0)}
                </Typography>
              </Tooltip>
            </li>
            <li>
              <Typography variant="h5">Demand</Typography>
              <Tooltip title={isProductAvailable ? 'Sum of adjusted demand YTD actuals + PDU prediction for time selection' : 'Please select a product first' }>
                <Typography>{HRNumbers.toHumanString(this.demand_units() || 0)}</Typography>
              </Tooltip>
            </li>
            <li>
              <Typography variant="h5">Sell-Out Units LY</Typography>
              <Tooltip title={isProductAvailable ? 'Sum of LY actuals for time selection (where available)' : 'Please select a product first' }>
                <Typography className="value-p">{HRNumbers.toHumanString(this.sell_out_units_ly() || 0)}</Typography>
              </Tooltip>
            </li>
            <li>
              <Typography variant="h5">Demand LY</Typography>
              <Tooltip title={isProductAvailable ? 'Sum of adjusted demand LY actuals for time selection' : 'Please select a product first' }>
                <Typography className="value-p">{HRNumbers.toHumanString(this.demand_units_ly() || 0)}</Typography>
              </Tooltip>
            </li>
            <li>
              <Typography variant="h5">%Variance</Typography>
              <Typography className={ varianceClass(this.sell_out_units(), this.sell_out_units_ly()) + " value-p" }>{displayABSVariance(this.sell_out_units(), this.sell_out_units_ly())}%</Typography>
            </li>
            <li>
              <Typography variant="h5">%Variance</Typography>
              <Typography  className={ varianceClass(this.demand_units(), this.demand_units_ly()) + " value-p" }>{displayABSVariance(this.demand_units(), this.demand_units_ly())}%</Typography>
            </li>
          </ul>
        </li>
      </ul>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(PDUData);

