import { getGranularity } from 'utils/map_filter_type_display'
import moment from 'moment'
import { store } from 'utils/configureStore';
import { getDataKey } from 'utils/map_filter_type_display'
import { datumForTime } from 'utils/datumForTime'

export const getFiscalNumber = (val) => {
  if (getGranularity() === 'weekly') {
    return val > 52 ? val - 52 : val
  } else {
    return val > 12 ? val - 12 : val
  }
}

export const getWeekName = (val) => {
  if (getGranularity() === 'weekly') {
    let weekData = Object.values(store.getState().product.products)[0][getDataKey()].find((data) => data.g_index === val)
    return (weekData && datumForTime(weekData)) ? "FW " + String(datumForTime(weekData).fisc_week).slice(4,6) : ''
  } else {
    let monthData = Object.values(store.getState().product.products)[0][getDataKey()].find((data) => data.g_index === val)
    return (monthData && datumForTime(monthData)) ? datumForTime(monthData).fisc_month_name : ''
  }
}

export const withYear = (term, val, showYear) => {
  if(showYear) {
    return term + getProductYear(val)
  } else {
    return term
  }
}

export const getSeasonName = (val, showYear) => {
  if (getFiscalNumber(val) === 1) {
    return withYear('H1', val, showYear)
  } else if (getFiscalNumber(val) === 7) {
    return withYear('H2', val, showYear)
  } else {
    return ''
  }
}

export const getQuarterName = (val, showYear) => {
  if (getFiscalNumber(val) === 1) {
    return withYear('Q1', val, showYear)
  } else if (getFiscalNumber(val) === 4) {
    return withYear('Q2', val, showYear)
  } else if (getFiscalNumber(val) === 7) {
    return withYear('Q3', val, showYear)
  } else if (getFiscalNumber(val) === 10) {
    return withYear('Q4', val, showYear)
  } else {
    return ''
  }
}

const getProductYear = (val) => {
  const products = store.getState().product.products
  if (products && Object.values(products).length && Object.values(products)[0][getDataKey()].length && Object.values(products)[0][getDataKey()].find((data) => data.g_index === val)) {
    return (' ' + Object.values(products)[0][getDataKey()].find((data) => data.g_index === val).date).slice(0, 5)
  }
  return ''
}

export const getSecondAxisName = (val) => {
  switch (store.getState().graph.filterPeriod) {
    case 'year':
      return ''
    case 'season':
      return getSeasonName(val)
    case 'quarter':
      return getQuarterName(val)
    case 'month':
      return getMonthName(val)
    case 'week':
      return getMonthName(val)
    default:
      break;
  }

  return getSeasonName(val)
}

export const getSecondAxisNameWithYear = (val) => {
  const datum = Object.values(store.getState().product.products)[0][getDataKey()].find((data) => data.g_index === val)
  if(!datum) {
    return ''
  }

  const dataKPI = datumForTime(datum)
  if (!dataKPI) {
    return ''
  }
  switch (store.getState().graph.filterPeriod) {
    case 'year':
      return ''
    case 'season':
      return getSeasonName(val, true)
    case 'quarter':
      return getQuarterName(val, true)
    case 'month':
      return getMonthName(val, dataKPI, true)
    case 'week':
      return getMonthName(val, dataKPI, true)
    default:
      break;
  }

  return getSeasonName(val)
}

export const getWeekMappingByMonth = (month) => {
  const weekMapping = Object.entries(store.getState().graph.calendarData).find((calendar, index) => {
    return calendar[1]['fiscal_month'] === month
  })
  return weekMapping && weekMapping[0]
}

export const getMonthName = (val, dataKPI, showYear) => {
  var fiscalWeek = val > 52 ? val - 52 : val
  const momentWeek = moment(fiscalWeek, 'WW')
  const fiscalData = store.getState().graph.calendarData[momentWeek.format('YYYYWW')]
  if (fiscalData && getWeekMappingByMonth(fiscalData['fiscal_month']) === momentWeek.format('YYYYWW')) {
    return showYear ? dataKPI.fisc_month_name + ' ' + String(dataKPI.fisc_week).slice(0, 4) : dataKPI.fisc_month_name
  } else {
    return ''
  }
}
