import { LOGIN_TYPE, LOGOUT_TYPE, SHOW_FLASH_MESSAGE } from 'utils/types'
export const AUTH = {
  email: 'sawan@vinsol.com',
  password: 'qwqwqwqw'
}

export const AuthenticateUser = (email, pwd) => {
  let isLoggedIn = (AUTH['email'] === email && AUTH['password'] === pwd)
  if (isLoggedIn) {
    return {
      type: LOGIN_TYPE,
      payload: { email, pwd }
    }
  } else {
    return {
      type: SHOW_FLASH_MESSAGE,
      payload: { message: 'Wrong email/password', severity: 'error' }
    }
  }
}

export const logOutUser = () => {
  return {
    type: LOGOUT_TYPE,
    payload: false
  }
}
