import React, { Component } from "react"
import { connect } from 'react-redux';
import { hideLoader } from 'actions/home';
import watch from 'redux-watch'
import { store } from 'utils/configureStore';

class Loader extends Component {

  componentDidMount() {
    let watchFilterPeriod = watch(store.getState, 'shared.showLoader')
    store.subscribe(watchFilterPeriod((newVal, oldVal, objectPath) => {
      if(newVal) {
        document.querySelector('body').classList.add('loader-body')
      } else {
        document.querySelector('body').classList.remove('loader-body')
      }
    }))
  }

  render() {
    return (<></>)
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    shared: state.shared
  }
}

export default connect(mapStateToProps, { hideLoader })(Loader);
