import { TOGGLE_KPI, REMOVE_PRODUCT, TOGGLE_ALERT, UPDATE_FILTER_PERIOD, UPDATE_GRAPH_DOMAIN, GOTO_PREVIOUS, GOTO_NEXT, GOTO_CURRENT, HANDLE_SELECTED_PRODUCT_CHANGE, UPDATE_CALENDAR_DATA, ADD_PRODUCT } from 'utils/types'
