const getSeasonRange = (currentDate) => {
  const startYear = moment(currentDate).startOf('year')
  if (moment(currentDate).quarter() <= 2) {
    return [startYear.clone().startOf('year').format('YYYYMM'), startYear.clone().startOf('year').add(5, 'months').format('YYYYMM')]
  } else {
    return [startYear.clone().startOf('year').add(6, 'months').format('YYYYMM'), startYear.clone().endOf('year').startOf('week').format('YYYYMM')]
  }
}

const maxYear = (year) => {
  return year === '2021' ? '2020' : year
}

const minDate = (filterPeriod, date) => {
  const maxDate = moment.max(moment().subtract(1, 'year').startOf('year'), date)
  switch (filterPeriod) {
    case 'year':
      return moment.min(moment().endOf('year').subtract(5, 'week'), maxDate)
    case 'season':
      return moment.min(moment().endOf('year').subtract(1, 'quarter'), maxDate)
    case 'quarter':
      return moment.min(moment().endOf('year').subtract(1, 'quarter'), maxDate)
    case 'month':
      return moment.min(moment().endOf('year').subtract(2, 'months'), maxDate)
    case 'week':
      return moment.min(moment().endOf('year').subtract(5, 'week').startOf('week'), maxDate)
    default:
      break;
  }
}

const getWeekMappingByMonth = (month, state) => {
  return Object.entries(state.calendarData).find((calendar, index) => {
    return calendar[1]['fiscal_month'] === month
  })[0]
}

const getLastWeekMappingByMonth = (month, state) => {
  const iteratableCalendarData = Object.entries(state.calendarData)
  const lastIndex = iteratableCalendarData.map((calendar, index) => {
    return !!(String(calendar[1]['fiscal_month']) === month && calendar[0] !== "null")
  }).lastIndexOf(true)
  return iteratableCalendarData[lastIndex] && iteratableCalendarData[lastIndex][0]
}

const getFirstMonth = (currentDate, state) => {
  return moment(getWeekMappingByMonth(moment(currentDate).startOf('month').format('YYYY0MM'), state), 'YYYYWW').endOf('week')
}

const getSecondMonth = (currentDate, state) => {
  return moment(getLastWeekMappingByMonth(moment(currentDate).add(2, 'month').endOf('month').format('YYYY0MM'), state), 'YYYYWW').format('YYYYWW')
}

const getNewGraphDomain = (filterPeriod, currentDate, state) => {
  switch (filterPeriod) {
    case 'year':
      return [moment(currentDate).startOf('year').format('YYYYMM'), moment(currentDate).endOf('year').subtract(2, 'days').format('YYYYMM')]
    case 'season':
      return getSeasonRange(currentDate)
    case 'quarter':
      return [minDate(filterPeriod, moment(currentDate).startOf('quarter').endOf('week')).format('YYYYMM'), moment(currentDate).add(1, 'quarter').endOf('quarter').startOf('week').format('YYYYMM')]
    case 'month':
      return [minDate(filterPeriod, getFirstMonth(currentDate, state)).format('YYYYWW'), getSecondMonth(currentDate, state)]
    case 'week':
      return [minDate(filterPeriod, moment(currentDate).startOf('week')).format('YYYYWW'), maxYear(moment(currentDate).add(5, 'weeks').endOf('week').format('YYYY')) + moment(currentDate).add(5, 'weeks').endOf('week').format('WW')]
    default:
      break;
  }
}

const gotoPreviousDate = (filterPeriod, state) => {
  switch (filterPeriod) {
    case 'year':
      return moment(state.currentDate).subtract(1, 'year').startOf('year')
    case 'season':
      return moment(state.currentDate).subtract(6, 'months').startOf('month')
    case 'quarter':
      return moment(state.currentDate).subtract(3, 'months').startOf('month')
    case 'month':
      return moment(state.currentDate).subtract(1, 'month').startOf('month')
    case 'week':
      return moment(state.currentDate).subtract(1, 'week').startOf('week')
    default:
      break;
  }
}

const gotoNextDate = (filterPeriod, state) => {
  switch (filterPeriod) {
    case 'year':
      return moment(state.currentDate).add(1, 'year')
    case 'season':
      return moment(state.currentDate).add(6, 'months')
    case 'quarter':
      return moment(state.currentDate).add(3, 'months')
    case 'month':
      return moment(state.currentDate).add(1, 'month')
    case 'week':
      return moment(state.currentDate).add(1, 'week').startOf('week')
    default:
      break;
  }
}

const processCalendarData = (data) => {
  const calendarData = {}
  data.KPI.forEach((kpi) => {
    calendarData[String(kpi.fisc_week)] = { fiscal_month: String(kpi.fisc_month), fiscal_year: String(kpi.fisc_month).slice(0, 4) }
  })
  return calendarData
}

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case TOGGLE_KPI:
      state.kpis[payload.kpi].selected = !state.kpis[payload.kpi].selected
      return deepmerge.all([state])
    case ADD_PRODUCT:
      const productsClone = deepmerge.all([state.savedProducts])
      if(!productsClone[payload.product]) {
        productsClone.push(payload)
      }
      return { ...state, savedProducts: productsClone }
    case TOGGLE_ALERT:
      return { ...state, showAlert: !state.showAlert }
    case UPDATE_FILTER_PERIOD:
      return { ...state, filterPeriod: payload, currentDate: minDate(payload, moment(state.currentDate)).toString(), graphDomain: getNewGraphDomain(payload, minDate(payload, moment(state.currentDate)), state) }
    case GOTO_PREVIOUS:
      const prevDate = minDate(state.filterPeriod, gotoPreviousDate(state.filterPeriod, state))
      return { ...state, currentDate: prevDate.toString(), graphDomain: getNewGraphDomain(state.filterPeriod, prevDate, state) }
    case GOTO_NEXT:
      const nextDate = minDate(state.filterPeriod, gotoNextDate(state.filterPeriod, state))
      return { ...state, currentDate: nextDate.toString(), graphDomain: getNewGraphDomain(state.filterPeriod, nextDate, state) }
    case GOTO_CURRENT:
      return { ...state, currentDate: moment().toString(), graphDomain: getNewGraphDomain(state.filterPeriod, moment(), state) }
    case UPDATE_GRAPH_DOMAIN:
      return { ...state, graphDomain: getNewGraphDomain(state) }
    case HANDLE_SELECTED_PRODUCT_CHANGE:
      return { ...state, selectedProductId: payload }
    case UPDATE_CALENDAR_DATA:
      return { ...state, calendarData: deepmerge.all([state.calendarData, processCalendarData(payload.data)]) }
    case REMOVE_PRODUCT:
      const removeProductData = deepmerge.all([state.savedProducts])
      const removeProductDataIndex = removeProductData.findIndex((val) => val.product === payload)
      if (removeProductDataIndex > -1) {
        removeProductData.splice(removeProductDataIndex, 1)
      }
      return { ...state, savedProducts: removeProductData }
    default:
      return state;
  }
}
