import React, { Component } from "react"
import { connect } from 'react-redux';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core';
import { hideConfirmBox } from 'actions/home'

class ConfirmBox extends Component {

  handleClose() {
    this.props.hideConfirmBox()
    this.props.onSave()
  }


  handleCancel() {
    this.props.hideConfirmBox()
    this.props.onCancel()
  }

  render() {

    const { confirmBoxData } = this.props.shared

    return (
      <Dialog
        open={confirmBoxData.open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className="cu-confirm-box"
      >
        <DialogTitle id="alert-dialog-title">{confirmBoxData.subject}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {confirmBoxData.body}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={() => this.handleCancel()} color="primary">
            Cancel
          </Button>
          <Button className="theme-button" variant="contained" onClick={() => this.handleClose()} autoFocus>
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return { shared: state.shared }
}

export default connect(mapStateToProps, { hideConfirmBox })(ConfirmBox);

