import React, { Component } from "react"

import {
  BarChart,
  Bar,
  LabelList
} from "recharts"

import { randomColor } from 'randomcolor'

import {
  Dialog,
  DialogContent,
  Typography,
  DialogTitle,
  DialogActions,
  IconButton,
} from "@material-ui/core"
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import { showConfirmBox } from 'actions/home'
import { hideAssortmentVariables, updateVariableData } from 'actions/graph'
import getCurrentWeek from 'utils/getCurrentWeek'
import ConfirmBox from 'components/shared/confirmBox'
import { getDataKey, getGranularity } from 'utils/map_filter_type_display'
import { assortmentName } from 'utils/assortmentName'
import NumberFormat from 'react-number-format';
import { Close as CloseIcon } from "@material-ui/icons"

import "./GraphDetailsDialog.scss"

const CustomizedLabel = ({ payload, x, y, value }) => {
  console.log(value)
  return <text x={27} y={y} dy={(value * 125)} font-size="10px" offset="5" class="recharts-text recharts-label" text-anchor="middle">
    <tspan>{value*100}</tspan>
  </text>;
}
class GraphDetailsDialog extends Component {

  constructor(props) {
    super(props)
    this.state = {
      demand: 0,
      sellout: 0,
      colors: {}
    }
  }

  demandData(props) {
    const selectedAssortment = (props.selectedAssortment && props.selectedAssortment[1]) || {}
    const { activeTooltipIndex } = props.product
    return Number((selectedAssortment[getDataKey()][activeTooltipIndex].demand_units_scatter || selectedAssortment[getDataKey()][activeTooltipIndex].demand_units_ytg || selectedAssortment[getDataKey()][activeTooltipIndex].demand_units_ytd).KPI)
  }

  selloutData(props) {
    const selectedAssortment = (props.selectedAssortment && props.selectedAssortment[1]) || {}
    const { activeTooltipIndex } = props.product
    return Number((selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_scatter || selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytg || selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytd).KPI)
  }

  componentWillReceiveProps(props, oldProps) {
    const selectedAssortment = (props.selectedAssortment && props.selectedAssortment[1]) || {}
    const { activeTooltipIndex } = props.product

    if (props.product.showVariables && selectedAssortment[getDataKey()] && selectedAssortment[getDataKey()][activeTooltipIndex] && !this.state.demand) {
      this.setState({ demand: this.demandData(props), sellout: this.selloutData(props) })
    }
  }

  showAlert() {
    this.props.showConfirmBox('Please Confirm!', "Are you sure, you want to override the prediction?")
  }

  handleSave() {
    this.props.updateVariableData({ demand: this.state.demand, sellout: this.state.sellout })
    this.handleClose()
  }

  handleClose() {
    this.setState({ demand: 0, sellout: 0, colors: [] })
    this.props.hideAssortmentVariables()
  }

  updateDemand({ formattedValue, value}) {
    if (value > 0) {
      this.setState({ demand: value })
    }
  }

  updateSellout({ formattedValue, value }) {
    if (value > 0) {
      this.setState({ sellout: value })
    }
  }

  getPointName(selectedAssortment, activeTooltipIndex) {
    if (!selectedAssortment) {
      return ''
    }
    const data = selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytg || selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytd
    if(getGranularity() === 'weekly') {
      return `${data.fisc_year} FISCAL WEEK ${String(data.fisc_week).slice(4,6)}`
    } else {
      return `${data.fisc_year} ${data.fisc_month_name}`
      // return moment(selectedAssortment[getDataKey()][activeTooltipIndex]['date'].slice(4, 6), 'MM').format('MMMM')
    }
  }

  isHistorical(selectedAssortment, activeTooltipIndex) {
    if (!selectedAssortment) {
      return ''
    }
    return selectedAssortment[getDataKey()][activeTooltipIndex].g_index <= Number(getCurrentWeek())
  }

  getHistoricalLabel(selectedAssortment, activeTooltipIndex) {
    if (!selectedAssortment) {
      return ''
    }
    return selectedAssortment[getDataKey()][activeTooltipIndex].g_index <= Number(getCurrentWeek()) ? 'Historical Values' : 'Predicted Values'
  }

  getDemandPieData() {
    return this.getPieData('demand_units_ytd') || this.getPieData('demand_units_ytg')
  }

  getSellOutPieData() {
    return this.getPieData('sell_out_units_ytd') || this.getPieData('sell_out_units_ytg')
  }

  getPieData(kpi) {
    const selectedAssortment = (this.props.selectedAssortment && this.props.selectedAssortment[1])
    // const colorList = {
    //   "ColumbusDay": "0.1",
    //   "LaborDay": "0.1",
    //   "MensPromoCalendar": "0.1",
    //   "MidDecemberPeak": "0.1",
    //   "USOtherHolidays": "0.1",
    //   "DailySeasonality": "0.1",
    //   "BlackFriday": "0.2",
    //   "WeeklySeasonality": "0.1",
    //   "Trend": "0.05",
    //   "Seasonality": "0.05",
    // }
    if (selectedAssortment && selectedAssortment[getDataKey()][this.props.product.activeTooltipIndex][kpi]) {
      let colorList = []
      const variable_importance = selectedAssortment[getDataKey()][this.props.product.activeTooltipIndex][kpi].variable_importance
      Object.keys(variable_importance).forEach((varKey) => {
        if (Number(variable_importance[varKey]) > 0) {
          colorList[varKey] = variable_importance[varKey]
        }
      })

      this.setColors(colorList)
      return colorList
    }
    return []
  }

  setColors(colorList) {
    Object.keys(colorList).forEach((color) => {
      if (!this.state.colors[color]) {
        let colorHash = {}
        colorHash[color] = randomColor()
        this.setState({ colors: { ...this.state.colors, ...colorHash } })
      }
      // this.state.colors[]
    })
  }

  render() {
    const selectedAssortment = (this.props.selectedAssortment && this.props.selectedAssortment[1]) || {}
    const selectedAssortmentKey = (this.props.selectedAssortment && JSON.parse(this.props.selectedAssortment[0]))

    const { showVariables, activeTooltipIndex } = this.props.product

    return (
      <>
        {showVariables && <Dialog
          aria-labelledby="dialog-title"
          open={showVariables}
          className="assortment-graph-dialog dialog dialog-modal"
        >
          <DialogTitle id="dialog-title">
            <IconButton size="small" onClick={() => this.handleClose()}>
              <CloseIcon />
            </IconButton>
          </DialogTitle>

          <DialogContent>
            <div className="">

              <div className="graph modal-graph">
                <Typography variant="h2">
                  {this.getPointName(selectedAssortment, activeTooltipIndex)}
                </Typography>

                <div>
                  {this.props.selectedAssortment && <div className="merchandise-val">
                    <>
                      {assortmentName(this.props.selectedAssortment[0])}
                    </>
                  </div>}
                </div>

                <div className="chart-modal-val align-center d-flex">
                  <div className="d-flex">
                    <div className="vertical-sec">
                      <Typography>100%</Typography>
                      <Typography>50%</Typography>
                      <Typography>0%</Typography>
                    </div>
                    <div className="chart-modal">
                      {selectedAssortment[getDataKey()] && <BarChart width={90} height={240} data={[this.getDemandPieData()]}
                        margin={{ top: 5, right: 30, left: 0, bottom: 5 }}>
                        { Object.keys(this.getDemandPieData()).map((var_imp) => {
                          return (
                            <Bar isAnimationActive={false} barSize={45} key={var_imp} stackId='demand' dataKey={var_imp} fill={this.state.colors[var_imp]}>
                              <LabelList dataKey={var_imp} position="center" content={CustomizedLabel} />
                            </Bar>)
                        }) }
                      </BarChart>
                      }
                      <span className="chart-label">Sell-Out</span>
                    </div>

                    <div className="chart-modal">
                      {selectedAssortment[getDataKey()] && <BarChart width={90} height={240} data={[this.getSellOutPieData()]}
                        margin={{ top: 5, right: 30, left: 0, bottom: 5 }}>
                        {Object.keys(this.getSellOutPieData()).map((var_imp) => {
                          return (
                            <Bar isAnimationActive={false} barSize={45} key={var_imp} stackId='sell_out_units' dataKey={var_imp} fill={this.state.colors[var_imp]}>
                              <LabelList position="center" dataKey={var_imp} content={CustomizedLabel} />
                              {/* <LabelList dy={this.getSellOutPieData()[var_imp] * 85} x={28} fontSize={'10px'} dataKey={var_imp} position="bottom" formatter={(val) => `${val * 100}%`} /> */}
                            </Bar>)
                        })}
                      </BarChart>}
                      <span className="chart-label">Demand</span>
                    </div>

                    <div className="color-state">
                      {Object.keys(this.state.colors).map((colorKey) => {
                        return(<div className="m-b-10 d-flex align-center" style={{ color: this.state.colors[colorKey] }} key={colorKey}>
                            <span style={{ background: this.state.colors[colorKey] }} key={colorKey}></span>
                            <Typography>{colorKey}</Typography>
                          </div>)
                      })}
                    </div>
                  </div>

                  <div className="chart-fileds">

                    {selectedAssortment[getDataKey()] && selectedAssortment[getDataKey()][activeTooltipIndex] && <ul>
                      <li className="labels m-b-15-hard">
                        <Typography variant="h3">Sell-Out</Typography>
                        <Typography variant="h3">Demand</Typography>
                      </li>

                      <li className="p-b-0-hard">
                        <Typography variant="h6">{this.getHistoricalLabel(selectedAssortment, activeTooltipIndex)}</Typography>
                        <Typography variant="h6">{this.getHistoricalLabel(selectedAssortment, activeTooltipIndex)}</Typography>
                      </li>

                      <li className="no-editable">
                        <input
                          name="graph-sellout-original"
                          id="graph-sellout-original"
                          readOnly
                          value={Number((selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytg || selectedAssortment[getDataKey()][activeTooltipIndex].sell_out_units_ytd).KPI).toLocaleString()}
                        />
                        <input
                          name="graph-demand-original"
                          id="graph-demand-original"
                          readOnly
                          value={Number((selectedAssortment[getDataKey()][activeTooltipIndex].demand_units_ytg || selectedAssortment[getDataKey()][activeTooltipIndex].demand_units_ytd).KPI).toLocaleString()}
                        />
                      </li>

                      {selectedAssortmentKey['planning_group_name'] && selectedAssortmentKey['category'] && !this.isHistorical(selectedAssortment, activeTooltipIndex) && <div>

                        <li className="p-b-0-hard">
                          <Typography variant="h6">My Adjustments</Typography>
                          <Typography variant="h6">My Adjustments</Typography>
                        </li>

                        <li className="p-t-0">
                          <NumberFormat
                            value={this.state.sellout}
                            onValueChange={(values) => this.updateSellout(values)}
                            allowNegative={false}
                            thousandSeparator={true}
                          />
                          <NumberFormat
                            value={this.state.demand}
                            onValueChange={(values) => this.updateDemand(values)}
                            allowNegative={false}
                            thousandSeparator={true}
                          />
                        </li>
                      </div>}
                    </ul>}
                    <DialogActions>
                      {selectedAssortmentKey['planning_group_name'] && selectedAssortmentKey['category'] && !this.isHistorical(selectedAssortment, activeTooltipIndex) && <Button autoFocus onClick={() => this.showAlert()} color="primary">
                        Save
                      </Button>}
                    </DialogActions>
                  </div>
                </div>
              </div>
            </div>


          </DialogContent>

        </Dialog>}
        <ConfirmBox onSave={() => this.handleSave()} onCancel={() => this.handleClose()} />
      </>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    selectedAssortment: Object.entries(state.product.products).find(([key, product]) => product.selected)
  }
}

export default connect(mapStateToProps, { showConfirmBox, hideAssortmentVariables, updateVariableData })(GraphDetailsDialog);
