import React, { Component } from "react"

import { Typography, Button } from "@material-ui/core"
import { AuthenticateUser } from 'actions/authentication/login'
import "./Login.scss"
import { connect } from 'react-redux';
import { showFlashMessage } from 'actions/home';

class Login extends Component {

  constructor(props) {
    super(props);
    this.state = { email: '', pwd: '' }
  }

  login() {
    this.props.AuthenticateUser(this.state.email, this.state.pwd)
  }

  render() {

    return (
      <div className="login">
        <div className="hero-section">
          <img src="/images/logo-vertical.png" alt='Levis' />
        </div>
        <div className="form-section">
          <form onSubmit={() => { this.login() }}>

            <Typography variant="h1">
              Login
            </Typography>

            <div className="field-section">
              <label>Email</label>
              <input
                name="email"
                type="text"
                placeholder="Enter your email here"
                onKeyPress={(event) => { if (event.which === 13) { this.login()  } }}
                onInput={(event) => { this.setState({ email: event.target.value }) }}
              />
            </div>

            <div className="field-section">
              <label>Password</label>
              <input
                name="password"
                type="password"
                onKeyPress={(event) => { if (event.which === 13) { this.login() } }}
                placeholder="Enter your password here"
                onInput={(event) => { this.setState({ pwd: event.target.value }) }}
              />

              <div>
                {/* <Link href="">Forgot your password?</Link> */}
              </div>
            </div>

            <div className="button-section">
              <Button color="primary" variant="contained" onClick={() => { this.login() }}>
                Login
              </Button>

              {/* <Button color="primary">Sign up</Button> */}
            </div>
          </form>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    auth: state.auth
  }
}

export default connect(mapStateToProps, { AuthenticateUser, showFlashMessage })(Login);
