import React, { Component } from "react"
import { connect } from 'react-redux';
import { Typography } from "@material-ui/core"
import HRNumbers from 'human-readable-numbers'
import { getTimeRange } from 'utils/getTimeRange'
import MAP_FILTER_TYPE_DISPLAY from 'utils/map_filter_type_display'
import { displayABSVariance, varianceClass } from 'utils/getVariance'
import {
  BarChart,
  Bar
} from "recharts"

class TimedData extends Component {
  splitData() {
    const isProductAvailable = (this.props.product && this.props.product.products[this.props.graph.selectedProductId])
    if (isProductAvailable) {
      const product = this.props.product.products[this.props.graph.selectedProductId][MAP_FILTER_TYPE_DISPLAY[this.props.graph.filterPeriod]['data']]
      return product.filter((pr) => pr.date >= this.props.graph.graphDomain[0] && pr.date <= this.props.graph.graphDomain[1])
    }
    return [{}]
  }

  demand_units() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + Number(((data.demand_units_ytg || data.demand_units_ytd) && (data.demand_units_ytg || data.demand_units_ytd).KPI) || 0)
    })
    return sum
  }

  demand_units_sas() {
    var sum = 0;
    this.splitData().forEach((data) => {
      sum = Number(sum) + Number((data.demand_units_sas && data.demand_units_sas.KPI) || 0)
    })
    return sum
  }

  render() {

    const { graph } = this.props;

    return (
      <ul className="kpis-section kpis-section-top">
        <li className="yearly paper sale-sec col-3">
          <Typography variant="h3" className="text-center text-uppercase">Item Plan Comparison</Typography>
          <Typography variant="h4" className="m-b-15-hard text-center">{getTimeRange(graph.graphDomain, graph.filterPeriod)}</Typography>
          <Typography variant="h4" className="m-b-15-hard text-center"></Typography>
          <div className="flex-row relative">
            <BarChart barGap={121} barSize={50} width={258} height={170} data={[{ name: 'demand_pred', value: this.demand_units_sas(), demand_value: this.demand_units() }]}
              margin={{ top: 5, right: 0, left: 20, bottom: 5 }}>
              <Bar isAnimationActive={false} dataKey="demand_value" fill="#79ade8">
                {/* <LabelList fontSize={'10px'} y={'135'} x={'53'} dataKey="demand_value" position="bottom" formatter={(val) => `${HRNumbers.toHumanString(val)} Demand Prediction`} /> */}
              </Bar>
              <Bar isAnimationActive={false} dataKey="value" fill="#ea9191">
                {/* <LabelList fontSize={'10px'} y={'135'} x={'225'} dataKey="value" position="bottom" formatter={(val) => `${HRNumbers.toHumanString(val)} Demand Plan`} /> */}
              </Bar>
            </BarChart>
            <div className="demand-label plan-label">
              {HRNumbers.toHumanString(this.demand_units())} <span>Demand Prediction</span>
            </div>
            <div className="demand-label pre-label">
              {HRNumbers.toHumanString(this.demand_units_sas())} <span>Demand Plan</span>
            </div>
            <div className="center-val variance-val">
              %variance
              <br/>
              <Typography className={varianceClass(this.demand_units(), this.demand_units_sas())}>
                {displayABSVariance(this.demand_units(), this.demand_units_sas())}%
              </Typography>
            </div>
          </div>
        </li>
      </ul>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(TimedData);

