import React, { Component } from "react"
import { connect } from 'react-redux';
import { Button } from "@material-ui/core"

class LocalstorageHelper extends Component {

  downloadLocalStorage() {
    const element = document.createElement("a");
    const file = new Blob([localStorage.getItem('persist:pr_app')], { type: 'text/plain' });
    element.href = URL.createObjectURL(file);
    element.download = "demand_sensing_configuration.txt";
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  }

  handleFileRead()  {
    localStorage.setItem('persist:pr_app', this.result)
    window.location.reload()
  }

  handleFileChosen(file) {
    const fileReader = new FileReader()
    fileReader.onloadend = this.handleFileRead
    fileReader.readAsText(file)
  }

  render() {

    return (
      <>
        <Button color="primary" className="send-button" onClick={() => this.downloadLocalStorage()}>
          Download Session
        </Button>
        <label htmlFor="config-file" className="upload-btn">
          Upload Session
        </label>
        <input className='hide' type="file" id='config-file' onChange={e => { this.handleFileChosen(e.target.files[0]) }} />
      </>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(LocalstorageHelper);

