import React, { Component } from "react"
import { connect } from 'react-redux';
import { Button } from "@material-ui/core"
import { CSVLink } from "react-csv";
import { getWeekName } from 'utils/getTimeLineHelper'
import { getDataKey } from 'utils/map_filter_type_display'
import { assortmentNameJSON } from 'utils/assortmentName'
import { displayVariance } from 'utils/getVariance'

class CSVGenerator extends Component {

  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
  }

  splitData(product) {
    return product[getDataKey()]
      .filter((pr) => pr.date >= this.props.graph.graphDomain[0] && pr.date <= this.props.graph.graphDomain[1])
      .sort((pr1, pr2) => { return pr1.g_index - pr2.g_index })

  }

  getKPI(datum) {
    return datum ? datum.KPI : 0
  }

  showKPIName(kpi, productKey) {
    kpi['Region'] = assortmentNameJSON(productKey).region
    kpi['Affiliate'] = assortmentNameJSON(productKey).affiliate
    kpi['Channel'] = assortmentNameJSON(productKey).channel
    kpi['Planning Group'] = assortmentNameJSON(productKey).planning_group_name
    kpi['Brand'] = assortmentNameJSON(productKey).brand
    kpi['Consumer'] = assortmentNameJSON(productKey).consumer
    kpi['Category'] = assortmentNameJSON(productKey).category
  }

  setData(event, done) {
    var finalData = []

    var sell_out_units = {}
    var sell_out_units_ly = {}
    var sell_out_units_ly_var = {}
    var sell_out_units_sas = {}
    var sell_out_units_sas_var = {}
    var demand_units = {}
    var demand_units_ly = {}
    var demand_units_ly_var = {}
    var demand_units_sas = {}
    var demand_units_sas_var = {}
    var shipments = {}

    Object.keys(this.props.product.products).forEach((productKey) => {
      this.showKPIName(sell_out_units, productKey)
      this.showKPIName(sell_out_units_ly, productKey)
      this.showKPIName(sell_out_units_sas, productKey)
      this.showKPIName(demand_units, productKey)
      this.showKPIName(demand_units_ly, productKey)
      this.showKPIName(demand_units_sas, productKey)
      this.showKPIName(shipments, productKey)
      this.showKPIName(sell_out_units_ly_var, productKey)
      this.showKPIName(sell_out_units_sas_var, productKey)
      this.showKPIName(demand_units_ly_var, productKey)
      this.showKPIName(demand_units_sas_var, productKey)

      sell_out_units['KPI'] = 'Sell-Out Units'
      sell_out_units_ly['KPI'] = 'Sell-Out Units LY'
      sell_out_units_sas['KPI'] = 'Sell-Out Units Plan'
      demand_units['KPI'] = 'Demand'
      demand_units_ly['KPI'] = 'Demand LY'
      demand_units_sas['KPI'] = 'Demand Plan'
      shipments['KPI'] = 'Shipped Units'
      sell_out_units_ly_var['KPI'] = '%Variance'
      sell_out_units_sas_var['KPI'] = '%Variance'
      demand_units_ly_var['KPI'] = '%Variance'
      demand_units_sas_var['KPI'] = '%Variance'

      this.splitData(this.props.product.products[productKey]).forEach(data => {
        const key = getWeekName(data.g_index)
        sell_out_units[key] = this.getKPI(data.sell_out_units_ytg) || this.getKPI(data.sell_out_units_ytd)
        sell_out_units_ly[key] = this.getKPI(data.sell_out_units_ly)
        sell_out_units_ly_var[key] = displayVariance(sell_out_units[key], sell_out_units_ly[key]) + '%'
        sell_out_units_sas[key] = this.getKPI(data.sell_out_units_sas)
        sell_out_units_sas_var[key] = displayVariance(sell_out_units[key], sell_out_units_sas[key]) + '%'

        demand_units[key] = this.getKPI(data.demand_units_ytg) || this.getKPI(data.demand_units_ytd)
        demand_units_ly[key] = this.getKPI(data.demand_units_ly)
        demand_units_ly_var[key] = displayVariance(demand_units[key], demand_units_ly[key]) + '%'
        demand_units_sas[key] = this.getKPI(data.demand_units_sas)
        demand_units_sas_var[key] = displayVariance(demand_units[key], demand_units_sas[key]) + '%'

        shipments[key] = this.getKPI(data.shipments)
      })

      // sell_out_units['1'] = assortmentNameJSON(productKey)
      // sell_out_units_ly['1'] = assortmentNameJSON(productKey)
      // sell_out_units_sas['1'] = assortmentNameJSON(productKey)
      // demand_units['1'] = assortmentNameJSON(productKey)
      // demand_units_ly['1'] = assortmentNameJSON(productKey)
      // demand_units_sas['1'] = assortmentNameJSON(productKey)
      // shipments['1'] = assortmentNameJSON(productKey)
      // sell_out_units_ly_var['1'] = assortmentNameJSON(productKey)
      // sell_out_units_sas_var['1'] = assortmentNameJSON(productKey)
      // demand_units_ly_var['1'] = assortmentNameJSON(productKey)
      // demand_units_sas_var['1'] = assortmentNameJSON(productKey)
      finalData.push(
        { ...sell_out_units },
        { ...sell_out_units_ly },
        { ...sell_out_units_ly_var },
        { ...sell_out_units_sas },
        { ...sell_out_units_sas_var },
        { ...demand_units },
        { ...demand_units_ly },
        { ...demand_units_ly_var },
        { ...demand_units_sas },
        { ...demand_units_sas_var },
        { ...shipments }
      )
    })
    this.setState({ data: finalData }, () => {
      done(true)
    })
  }

  render() {

    const products = this.props.product.products

    return (
      <>
        {products && Object.keys(products).length > 0 &&
        <CSVLink
          data={this.state.data}
          asyncOnClick={true}
          onClick={(event, done) => this.setData(event, done)}
          className="text-transparent export-csv"
          filename={'csv-data.csv'}>
          <Button color="primary" className="send-button">
          </Button>
        </CSVLink>}
        {(products && !Object.keys(products).length) &&
          <a class="text-transparent export-csv">
            <Button color="primary" className="send-button">
            </Button>
          </a>
        }
      </>
    )
  }
}

const mapStateToProps = (state, _ownProps) => {
  return {
    product: state.product,
    graph: state.graph
  }
}

export default connect(mapStateToProps, {})(CSVGenerator);
