import React from "react"

export function CheckBoxUnchecked(props) {
  return (<svg width={props.size || "32"} height={props.size || "32"} viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg">
    <rect x="1" y="1" width="30" height="30" rx="1" stroke={props.color} strokeWidth="2" />
  </svg>)
}
