import { store } from 'utils/configureStore';

export const getGranularity = () => {
  return MAP_FILTER_TYPE_DISPLAY[store.getState().graph.filterPeriod]['granularity']
}

export const getDataKey = () => {
  return MAP_FILTER_TYPE_DISPLAY[store.getState().graph.filterPeriod]['data']
}

const MAP_FILTER_TYPE_DISPLAY = {
  'year': {
    granularity: 'monthly',
    data: 'weekly_data'
  },
  'season': {
    granularity: 'monthly',
    data: 'weekly_data'
  },
  'quarter': {
    granularity: 'monthly',
    data: 'weekly_data'
  },
  'month': {
    granularity: 'weekly',
    data: 'data'
  },
  'week': {
    granularity: 'weekly',
    data: 'data'
  },
}

export default MAP_FILTER_TYPE_DISPLAY;
