import { LOGIN_TYPE, LOGOUT_TYPE } from 'utils/types'

const INITIAL_STATE = {
  loggedIn: false
}

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case LOGIN_TYPE:
      return { ...state, loggedIn: true }
    case LOGOUT_TYPE:
      return { ...state, loggedIn: false }
    default:
      return state;
  }
}
