import { UPDATE_DATA, SET_INITIAL_DATA, SHOW_ASSORTMENT_ALERTS, HIDE_ASSORTMENT_ALERTS, SHOW_ASSORTMENT_VARIABLES, HIDE_ASSORTMENT_VARIABLES, UPDATE_VARIABLE_DATA, ADD_PRODUCT, REMOVE_PRODUCT } from 'utils/types'
import deepmerge from 'deepmerge'
import { GetYearIndex, GetMonthIndex } from 'utils/getYearIndex'

const INITIAL_STATE = {
  products: { },
  showVariables: false,
  showProductAlert: false,
  activeTooltipIndex: 0
}

const getValue = (date, newState, product, granularity) => {
  return newState[product][granularity].find((val) => { return val.date === date })
}

export const getNestedObject = function (data, deepPath) {
  var newObject = data
  for (var path of deepPath.split('.')) {
    newObject = newObject[path]
  }
  return newObject
}

const getType = (type, data) => {
  if(type === 'demand_units_ytg' || type === 'sell_out_units_ytg') {
    return data.predicted === 'True' ? type : null
  } else if (type === 'demand_units_ytd' || type === 'sell_out_units_ytd') {
    return data.predicted === 'False' ? type : null
  }
  return type
}

const addDataForGraph = (dataSet, newState, payload) => {
  if (payload.helper[0].type === 'sell_out_units_ytd') {
    let presetValue = dataSet.KPI.filter((data) => data.predicted === 'False')
    // newState[payload.product][payload.granularity].max()
    if (presetValue.length) {
      pushIntoData(dataSet, newState, payload, presetValue[presetValue.length - 1], 'sell_out_units_ytg', 'sell_out_units_ytg')
    }
  }
  if (payload.helper[0].type === 'demand_units_ytd') {
    let presetValue = dataSet.KPI.filter((data) => data.predicted === 'False')
    // newState[payload.product][payload.granularity].max()
    if (presetValue.length) {
      pushIntoData(dataSet, newState, payload, presetValue[presetValue.length - 1], 'demand_units_ytg', 'demand_units_ytg')
    }
  }
}

const pushIntoData = (dataSet, newState, payload, data, type, graphKey) => {
  var date;
  if (payload.helper[0].type === 'demand_units_ly' || payload.helper[0].type === 'sell_out_units_ly') {
    date = payload.granularity === 'data' ? String(Number(String(data.fisc_week).slice(0, 4)) + 1) + String(data.fisc_week).slice(4, 6) : String(Number(String(data.fisc_month).slice(0, 4)) + 1) + String(data.fisc_month).slice(5, 7)
  } else {
    date = payload.granularity === 'data' ? String(data.fisc_week) : String(data.fisc_month).slice(0, 4) + String(data.fisc_month).slice(5, 7)
  }
  let currentValue = getValue(date, newState, payload.product, payload.granularity)
  if (!currentValue) {
    currentValue = { date: date, g_index: Number(date) - getIndex(payload, date.slice(0, 4)) }
    newState[payload.product][payload.granularity].push(currentValue)
  }
  if (graphKey) {
    currentValue[graphKey] = { ...data, KPI: parseInt(data.KPI) }
  }
  if (getType(type, data)) {
    currentValue[getType(type, data)] = { ...data, KPI: parseInt(data.KPI) }
  }
}

const getIndex = (payload, year) => {
  return payload.granularity === 'weekly_data' ? GetMonthIndex(year) : GetYearIndex(year)
}

const processData = (dataSet, newState, payload) => {
  payload.helper.forEach(({parse, type}) => {
    newState[payload.product]['total'][payload.year][type] = dataSet.total
    // const getIndex = payload.granularity === 'weekly_data' ? GetMonthIndex(payload.year) : GetYearIndex(payload.year)
    dataSet.KPI.forEach((data) => {
      pushIntoData(dataSet, newState, payload, data, type)
    })
  })
  addDataForGraph(dataSet, newState, payload)
  newState[payload.product][payload.granularity] = newState[payload.product][payload.granularity].sort((a, b) => { return a.g_index - b.g_index })
}

const setData = () => {
  return []
}

const selectProduct = (product_id, state) => {
  const showAssortData = deepmerge.all([state.products])
  showAssortData[product_id]['selected'] = true
  return showAssortData
}

const setProductVariableProduct = (state, payload) => {
  const showAssortData = deepmerge.all([state.products])
  showAssortData[payload.product_id]['selected'] = true
  return showAssortData
}

const hideProduct = (state) => {
  const hideAssortData = deepmerge.all([state.products])
  Object.entries(hideAssortData).forEach(([key, product]) => product.selected = false)
  return hideAssortData
}

const updateVariableData = (state, payload) => {
  const hideAssortData = deepmerge.all([state.products])
  const productKey = Object.entries(hideAssortData).find(([key, product]) => product.selected)[0]
  const productDatum = hideAssortData[productKey][payload.dataKey][state.activeTooltipIndex]
  if (payload.demand !== productDatum.demand_units_ytg.KPI) {
    productDatum.demand_units_scatter = { date: productDatum.date, g_index: productDatum.g_index, KPI: payload.demand }
  }
  if (payload.sellout !== productDatum.sell_out_units_ytg.KPI) {
    productDatum.sell_out_units_scatter = { date: productDatum.date, g_index: productDatum.g_index, KPI: payload.sellout }
  }
  return hideAssortData
}

const initialData = () => {
  return {
    data: setData(),
    weekly_data: setData(),
    total: {
      2019: {},
      2020: {}
    },
    selected: false,
    detail: {
      name: 'Levis',
      category: 'Category'
    }
  }
}

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case REMOVE_PRODUCT:
      const removeProductData = deepmerge.all([state.products])
      delete removeProductData[payload]
      return { ...state, products: removeProductData }
    case ADD_PRODUCT:
      const newProduct = {}
      newProduct[payload.product] = initialData()
      return { ...state, products: deepmerge.all([state.products, newProduct]) }
    case SET_INITIAL_DATA:
      const initProductData = deepmerge.all([state.products])
      payload.products.forEach((product) => {
        if (!initProductData[product.product]) {
          initProductData[product.product] = initialData()
        }
      })
      return { ...state, products: initProductData }
    case SHOW_ASSORTMENT_ALERTS:
      return { ...state, products: selectProduct(payload, state), showProductAlert: true }
    case HIDE_ASSORTMENT_ALERTS:
      return { ...state, products: hideProduct(state), showProductAlert: false }
    case SHOW_ASSORTMENT_VARIABLES:
      return { ...state, products: setProductVariableProduct(state, payload), showVariables: true, activeTooltipIndex: payload.activeTooltipIndex }
    case HIDE_ASSORTMENT_VARIABLES:
      return { ...state, products: hideProduct(state), showVariables: false, activeTooltipIndex: 0 }
    case UPDATE_DATA:
      const newData = deepmerge.all([state.products])
      processData(payload.data, newData, payload)
      return { ...state, products: newData }
    case UPDATE_VARIABLE_DATA:
      return { ...state, products: updateVariableData(state, payload) }
    default:
      return state;
  }
}
