import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import React from "react"

const VARIANCE_THRESHHOLD = 10

export const getVariance = (val1, val2) => {
  const FirstNumber = Number(val1)
  const SecondNumber = Number(val2)
  if(FirstNumber && SecondNumber) {
    return ((1 - (SecondNumber / FirstNumber)) * 100)
  } else {
    return 0
  }
}

export const displayVariance = (val1, val2) => {
  return getVariance(val1, val2).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })
}

export const varianceClass = (val1, val2) => {
  return displayVariance(val1, val2) >= 0 ? 'positive-val' : 'negative-val'
}

export const varianceArrow = (val1, val2) => {
  if (displayVariance(val1, val2) >= 0) {
    return <ArrowDropUpIcon style={{ fontSize: 40, color: 'green' }} />
  } else {
    return <ArrowDropDownIcon style={{ fontSize: 40, color: 'red' }} />
  }
}

export const displayABSVariance = (val1, val2) => {
  return Math.abs(getVariance(val1, val2)).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })
}

export const varianceExceedsThreshhold = (val1, val2) => {
  return Math.abs(getVariance(val1, val2)) > VARIANCE_THRESHHOLD
}
