import React, { Component } from "react"
import { connect } from 'react-redux';
import { Radio } from "@material-ui/core"
import { Error as ErrorIcon } from '@material-ui/icons';
import DeleteIcon from '@material-ui/icons/Delete';
import { GraphHelper } from 'utils/graphHelper';
import getCurrentWeek from 'utils/getCurrentWeek'
import { showAssortmentAlerts, showAssortmentVariables, handleSelectedProductChange, removeAssortment } from 'actions/graph'
import { getGranularity, getDataKey } from 'utils/map_filter_type_display'
import { GetYearIndex, GetMonthIndex } from 'utils/getYearIndex'
import moment from 'moment'
import { alertList } from 'utils/alertList'
import { showFlashMessage } from 'actions/home';
import { assortmentName } from 'utils/assortmentName'
import HRNumbers from 'human-readable-numbers'
import { datumForTime } from 'utils/datumForTime'

import {
  CartesianGrid,
  Line,
  Scatter,
  ComposedChart,
  ResponsiveContainer,
  Tooltip as ReTooltip,
  XAxis,
  YAxis,
  ReferenceLine
} from "recharts"


class GraphLine extends Component {

  constructor(props) {
    super(props)
    this.state = {
      chartTheme: {
        paddingV: 10,
        paddingH: 40,
        colorRef: "#f6617b",
        strokeWidth: 2
      }
    }
  }

  currentTestStartOfMonthDate() {
    var date = new Date()
    if ((this.props.graph.filterPeriod === 'year') || (this.props.graph.filterPeriod === 'season')) {
      date.setDate(1)
    }
    date.setHours(0, 0, 0, 0)
    return date
  }

  handleAssortmentChartClick = (data, assortmentKey) => {
    if (!data) return

    // if (data.activeLabel <= Number(getCurrentWeek())) return

    if (!(this.props.graph.kpis.sell_out_units.selected || this.props.graph.kpis.demand_units.selected)) return

    this.props.showAssortmentVariables(assortmentKey, data.activeTooltipIndex)
  }

  currentTestStartOfMonthTime() {
    return this.currentTestStartOfMonthDate().getTime()
  }

  addAssortmentInformation(assortment, assortmentKey, html) {
    if (alertList(assortment) && alertList(assortment).length > 0) {
      return (
        this.props.graph.showAlert ? (
          <div className="details notsimple">
            {html}
          </div>
        ) : (<div className="details simple">
          {html}
        </div>)
      )
    }

    return <div className="details">{html}</div>
  }

  removeAssortment(assortmentKey) {
    this.props.removeAssortment(assortmentKey)
    this.props.showFlashMessage('Assortment successfully removed', 'success')
  }

  handleAssortmentDetailClick(assortmentKey) {
    if (this.props.graph.showAlert) {
      this.props.showAssortmentAlerts(assortmentKey)
    }
  }

  CustomTooltip = ({ active, payload, label }) => {
    const { kpis } = this.props.graph
    if (active && payload && payload[0] && payload[0].payload) {
      const data = payload[0].payload
      return (
        <div className="custom-tooltip">
          <p className="label">{`${this.displayTimeName(label, data)}`}</p>
          {kpis.sell_out_units.selected && <p className="label">{`${kpis.sell_out_units.name}: ${this.addData(data.sell_out_units_ytd && data.sell_out_units_ytd.KPI, data.sell_out_units_ytg && data.sell_out_units_ytg.KPI)}`}</p>}
          {kpis.sell_out_units_ly.selected && <p className="label">{`${kpis.sell_out_units_ly.name}: ${this.addData(data.sell_out_units_ly && data.sell_out_units_ly.KPI)}`}</p>}
          {kpis.sell_out_units_sas.selected && <p className="label">{`${kpis.sell_out_units_sas.name}: ${this.addData(data.sell_out_units_sas && data.sell_out_units_sas.KPI)}`}</p>}
          {kpis.demand_units.selected && <p className="label">{`${kpis.demand_units.name}: ${this.addData(data.demand_units_ytd && data.demand_units_ytd.KPI, data.demand_units_ytg && data.demand_units_ytg.KPI)}`}</p>}
          {kpis.demand_units_ly.selected && <p className="label">{`${kpis.demand_units_ly.name}: ${this.addData(data.demand_units_ly && data.demand_units_ly.KPI)}`}</p>}
          {kpis.demand_units_sas.selected && <p className="label">{`${kpis.demand_units_sas.name}: ${this.addData(data.demand_units_sas && data.demand_units_sas.KPI)}`}</p>}
          {kpis.shipment.selected && <p className="label">{`${kpis.shipment.name}: ${this.addData(data.shipments && data.shipments.KPI)}`}</p>}
        </div>
      )
    }

    return null
  }

  addData(metric1, metric2) {
    return Number((Number(metric1) || 0) || (Number(metric2) || 0)).toLocaleString()
  }

  graphCenter() {
    return parseInt((this.props.graph.graphDomain[1] - this.props.graph.graphDomain[0]) / 2)
  }

  getMaxWeek(dataMax) {
    if (getGranularity() === 'weekly') {
      return this.props.graph.graphDomain[1] - GetYearIndex(this.props.graph.graphDomain[1].slice(0, 4))
    } else {
      return this.props.graph.graphDomain[1] - GetMonthIndex(this.props.graph.graphDomain[1].slice(0, 4))
    }
  }

  getMinWeek(dataMin) {
    if (getGranularity() === 'weekly') {
      return this.props.graph.graphDomain[0] - GetYearIndex(this.props.graph.graphDomain[0].slice(0, 4))
    } else {
      return this.props.graph.graphDomain[0] - GetMonthIndex(this.props.graph.graphDomain[0].slice(0, 4))
    }
  }

  displayTimeName(val, data) {
    if (getGranularity() === 'weekly') {
      return 'FW' + String(datumForTime(data).fisc_week).slice(4, 6) + ' ' + String(datumForTime(data).fisc_week).slice(0, 4)
    } else {
      return datumForTime(data).fisc_month_name + ' ' + String(datumForTime(data).fisc_month).slice(0, 4)
    }
  }

  getWeekName(val) {
    if(val > 52) {
      return `FW ${val - 52}`
    }
    return `FW ${val}`
  }

  getFullWeekName(val) {
    if(val > 52) {
      return `Fiscal Week ${val - 52}`
    }
    return `Fiscal Week ${val}`
  }

  getFullMonthName(val) {
    var finalMonth = val
    if(val > 12) {
      finalMonth = val - 12
    }
    return 'Month: ' + moment(finalMonth, 'MM').format('MMM')
  }

  getGraphValue(datum, key) {
    return datum[key] && datum[key]['KPI']
  }

  getTickCount() {
    if (getGranularity() === 'weekly') {
      return (this.props.graph.graphDomain[1] - GetYearIndex(this.props.graph.graphDomain[1].slice(0, 4))) - (this.props.graph.graphDomain[0] - GetYearIndex(this.props.graph.graphDomain[0].slice(0, 4)) - 1)
    } else {
      return (this.props.graph.graphDomain[1] - GetMonthIndex(this.props.graph.graphDomain[1].slice(0, 4))) - (this.props.graph.graphDomain[0] - GetMonthIndex(this.props.graph.graphDomain[0].slice(0, 4)) - 1)
    }
  }

  render() {

    const { assortment, assortmentKey } = this.props;
    const { kpis, selectedProductId } = this.props.graph

    return (<>
      <li>
        <div className="checkbox-graph d-flex flex-column align-center">
          <Radio
            checked={selectedProductId === assortmentKey}
            onChange={(event) => this.props.handleSelectedProductChange(event.target.value)}
            value={assortmentKey}
            inputProps={{ 'aria-label': 'A' }}
            name="radio-button-product-kpi"
            label={'TEST ME'}
            className="inputbox-icon"
            disableRipple
          />

          {this.props.graph.showAlert && alertList(assortment) && alertList(assortment).length > 0 && <ErrorIcon
            className="icon info-icon pointer"
            onClick={() => this.handleAssortmentDetailClick(assortmentKey)}
          />}
        </div>
        {this.addAssortmentInformation(
          assortment,
          assortmentKey,
          <>
            {/* <img
              alt={assortment.detail.name}
              src={`https://lsco.scene7.com/is/image/lsco/levis/clothing/${assortment.detail.image_id ||
                assortment.detail.id}-front-pdp.jpg?$grid_desktop_bottoms$`}
              onClick={() => this.handleAssortmentDetailClick(assortmentKey)}
            /> */}
            <div className="merchandise-val"
              onClick={() => this.handleAssortmentDetailClick(assortmentKey)}
            >
                {assortmentName(assortmentKey)}
            </div>
            <DeleteIcon fontSize="small" onClick={() => this.removeAssortment(assortmentKey)}></DeleteIcon>
          </>,
        )}

        <ResponsiveContainer className="graph">
          <ComposedChart
            margin={{
              top: 0,
              bottom: 0,
              right: -15,
              left: this.state.chartTheme.paddingH,
            }}
            data={assortment[getDataKey()]}
            onClick={data =>
              this.handleAssortmentChartClick(data, assortmentKey)
            }
          >
            <XAxis
              dataKey="g_index"
              type="number"
              domain={[dataMin => { return this.getMinWeek(dataMin) }, dataMax => { return this.getMaxWeek(dataMax) }]}
              tickFormatter={val => val}
              tickCount={this.getTickCount()}
              allowDataOverflow={true}
              hide={true}
            />

            <ReTooltip content={(param) => this.CustomTooltip(param)} />
            <YAxis
              allowDataOverflow={false}
              fontSize={'10px'}
              padding={{
                top: this.state.chartTheme.paddingV,
                bottom: this.state.chartTheme.paddingV,
              }}
              tickFormatter={(val) => { return HRNumbers.toHumanString(val) }}
              orientation="right"
            />
            <CartesianGrid horizontal={false} />
            <ReferenceLine
              x={getCurrentWeek()}
              stroke={this.state.chartTheme.colorRef}
              strokeWidth={this.state.chartTheme.strokeWidth}
            />
            {/* GraphHelper['sell_out_units_ytd']['name'] */}
            {kpis.sell_out_units.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['sell_out_units_ytd']['name'])}
              stroke={GraphHelper['sell_out_units_ytd']['stroke']}
              dot={false}
              strokeWidth={GraphHelper['sell_out_units_ytd']['strokeWidth']}
            />}
            {kpis.sell_out_units.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['sell_out_units_ytg']['name'])}
              stroke={GraphHelper['sell_out_units_ytg']['stroke']}
              strokeWidth={GraphHelper['sell_out_units_ytg']['strokeWidth']}
              dot={false}
              strokeDasharray="5 5"
            />}
            {kpis.sell_out_units_ly.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['sell_out_units_ly']['name'])}
              stroke={GraphHelper['sell_out_units_ly']['stroke']}
              dot={false}
              strokeWidth={GraphHelper['sell_out_units_ly']['strokeWidth']}
            />}
            {kpis.sell_out_units_sas.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['sell_out_units_sas']['name'])}
              stroke={GraphHelper['sell_out_units_sas']['stroke']}
              strokeWidth={GraphHelper['sell_out_units_sas']['strokeWidth']}
              dot={false}
            />}
            {kpis.sell_out_units.selected && <Scatter
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['sell_out_units_scatter']['name'])}
              stroke={GraphHelper['sell_out_units_scatter']['stroke']}
              strokeWidth={GraphHelper['sell_out_units_scatter']['strokeWidth']}
              dot={false}
            />}
            {kpis.demand_units.selected && <Scatter
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['demand_units_scatter']['name'])}
              stroke={GraphHelper['demand_units_scatter']['stroke']}
              strokeWidth={GraphHelper['demand_units_scatter']['strokeWidth']}
              dot={false}
            />}
            {kpis.demand_units.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['demand_units_ytd']['name'])}
              stroke={GraphHelper['demand_units_ytd']['stroke']}
              dot={false}
              strokeWidth={GraphHelper['demand_units_ytd']['strokeWidth']}
            />}
            {kpis.demand_units.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['demand_units_ytg']['name'])}
              stroke={GraphHelper['demand_units_ytg']['stroke']}
              strokeWidth={GraphHelper['demand_units_ytg']['strokeWidth']}
              dot={false}
              strokeDasharray="5 5"
            />}
            {kpis.demand_units_ly.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['demand_units_ly']['name'])}
              stroke={GraphHelper['demand_units_ly']['stroke']}
              dot={false}
              strokeWidth={GraphHelper['demand_units_ly']['strokeWidth']}
            />}
            {kpis.demand_units_sas.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['demand_units_sas']['name'])}
              stroke={GraphHelper['demand_units_sas']['stroke']}
              strokeWidth={GraphHelper['demand_units_sas']['strokeWidth']}
              dot={false}
            />}
            {kpis.shipment.selected && <Line
              type="monotone"
              dataKey={(datum) => this.getGraphValue(datum, GraphHelper['shipments']['name'])}
              stroke={GraphHelper['shipments']['stroke']}
              dot={false}
              strokeWidth={GraphHelper['shipments']['strokeWidth']}
            />}
          </ComposedChart>
        </ResponsiveContainer>
      </li>
    </>)
  }
}
const mapStateToProps = (state, _ownProps) => {
  return {
    graph: state.graph
  }
}

export default connect(mapStateToProps, { showAssortmentAlerts, showAssortmentVariables, handleSelectedProductChange, removeAssortment, showFlashMessage })(GraphLine);
