import moment from 'moment'
import { getGranularity } from 'utils/map_filter_type_display'

const getCurrentWeek = () => {
  if (getGranularity() === 'weekly') {
    return Number(moment().format('WW')) + 52;
  } else {
    return Number(moment().format('MM')) + 12;
  }
}

export default getCurrentWeek
