import { SHOW_ASSORT_BROWSER, HIDE_ASSORT_BROWSER, UPDATE_MERCH_DATA, UPDATE_REGION_DATA } from 'utils/types'

const INITIAL_STATE = {
  assortmentDialogOpen: false,
  merchDetails: {},
  regionDetails: {}
}

const randomNumber = () => {
  return 'r' + Math.floor(Math.random() * 10000000).toString();
}

const processMerch = (hierarchy) => {
  return {
    Brand: hierarchy.Brand.filter((br) => Object.keys(br)[0] === 'LEVIS').map((brand) => {
      const brandNodeId = randomNumber()
      return {
        name: Object.keys(brand)[0],
        id: Object.values(brand)[0],
        nodeId: brandNodeId,
        displayName: Object.keys(brand)[0],
        uniqueId: { brand: Object.keys(brand)[0] },
        Consumer: hierarchy.Consumer.map((consumer) => {
          const consumerNodeId = randomNumber()
          return {
            name: Object.keys(consumer)[0],
            id: Object.values(consumer)[0],
            nodeId: consumerNodeId,
            displayName: `${Object.keys(brand)[0]} / ${Object.keys(consumer)[0]}`,
            breadcrumb: [{ name: Object.keys(brand)[0], id: brandNodeId }, { name: Object.keys(consumer)[0], id: consumerNodeId }],
            uniqueId: { brand: Object.keys(brand)[0], consumer: Object.keys(consumer)[0] },
            Category: hierarchy.Category.map((category) => {
              const categoryNodeId = randomNumber()
              return {
                name: Object.keys(category)[0],
                id: Object.values(category)[0],
                nodeId: categoryNodeId,
                breadcrumb: [{ name: Object.keys(brand)[0], id: brandNodeId }, { name: Object.keys(consumer)[0], id: consumerNodeId }, { name: Object.keys(category)[0], id: categoryNodeId }],
                displayName: `${Object.keys(brand)[0]} / ${Object.keys(consumer)[0]} / ${Object.keys(category)[0]}`,
                uniqueId: { brand: Object.keys(brand)[0], consumer: Object.keys(consumer)[0], category: Object.keys(category)[0] }
              }
            })
          }
        })
      }
    })
  }
}

const processRegionData = (data) => {
  return {
    region: data.regions.map((region) => {
      const regionNodeId = randomNumber()
      return {
        name: region.name,
        id: region.name,
        nodeId: regionNodeId,
        displayName: region.name,
        uniqueId: { region: region.name },
        affiliate: region.affiliates.map((affiliateData) => {
          const affiliateNodeId = randomNumber()
          return {
            name: affiliateData.name,
            id: affiliateData.affiliate_code,
            nodeId: affiliateNodeId,
            displayName: `${region.name} / ${affiliateData.name}`,
            breadcrumb: [{ name: region.name, id: regionNodeId }, { name: affiliateData.name, id: affiliateNodeId }],
            uniqueId: { region: region.name, affiliate: affiliateData.name },
            channels: affiliateData.channels.map((channel) => {
              const channelNodeId = randomNumber()
              return {
                name: channel.name,
                id: channel.name,
                nodeId: channelNodeId,
                displayName: `${region.name} / ${affiliateData.name} / ${channel.name}`,
                breadcrumb: [{ name: region.name, id: regionNodeId }, { name: affiliateData.name, id: affiliateNodeId }, { name: channel.name, id: channelNodeId }],
                uniqueId: { region: region.name, affiliate: affiliateData.name, channel: channel.name },
                planning_groups: channel.planning_groups.map((planning_group) => {
                  const planningGroupNodeId = randomNumber()
                  return {
                    name: Object.keys(planning_group)[0],
                    id: Object.values(planning_group)[0],
                    nodeId: planningGroupNodeId,
                    displayName: `${region.name} / ${affiliateData.name} / ${channel.name} / ${Object.keys(planning_group)[0]}`,
                    breadcrumb: [{ name: region.name, id: regionNodeId }, { name: affiliateData.name, id: affiliateNodeId }, { name: channel.name, id: channelNodeId }, { name: Object.keys(planning_group)[0], planningGroupNodeId }],
                    uniqueId: { region: region.name, affiliate: affiliateData.name, channel: channel.name, planning_group_name: Object.keys(planning_group)[0] },
                  }
                })
              }
            })
          }
        })
      }
    })
  }
}

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case SHOW_ASSORT_BROWSER:
      return { ...state, assortmentDialogOpen: true }
    case HIDE_ASSORT_BROWSER:
      return { ...state, assortmentDialogOpen: false }
    case UPDATE_MERCH_DATA:
      return { ...state, merchDetails: processMerch(payload.data.Hierarchy) }
    case UPDATE_REGION_DATA:
      return { ...state, regionDetails: processRegionData(payload.data) }
    default:
      return state;
  }
}
