import { SHOW_FLASH_MESSAGE, HIDE_FLASH_MESSAGE, SHOW_CONFIRM_BOX, HIDE_CONFIRM_BOX, SHOW_LOADER, HIDE_LOADER } from 'utils/types'

const INITIAL_STATE = {
  flash: {
    alertOpen: false,
    alertMessage: '',
    severity: 'success'
  },
  confirmBoxData: {
    open: false,
    subject: '',
    body: ''
  },
  showLoader: false
}

export default (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case SHOW_FLASH_MESSAGE:
      return { ...state, flash: { alertOpen: true, alertMessage: payload.message, severity: payload.severity } }
    case HIDE_FLASH_MESSAGE:
      return { ...state, flash: { alertOpen: false, alertMessage: '' } }
    case SHOW_CONFIRM_BOX:
      return { ...state, confirmBoxData: { open: true, subject: payload.subject, body: payload.body } }
    case HIDE_CONFIRM_BOX:
      return { ...state, confirmBoxData: { open: false, subject: '', body: '' } }
    case SHOW_LOADER:
      return { ...state, showLoader: true }
    case HIDE_LOADER:
      return { ...state, showLoader: false }
    default:
      return state;
  }
}
