import { getDataKey } from 'utils/map_filter_type_display'
import { varianceExceedsThreshhold } from 'utils/getVariance'

export const alertList = (assortment) => {
  return assortment[getDataKey()].filter((data) => {
    if (data.sell_out_units_ytg && data.sell_out_units_ytg.KPI && data.sell_out_units_sas && data.sell_out_units_sas.KPI && varianceExceedsThreshhold(data.sell_out_units_ytg.KPI, data.sell_out_units_sas.KPI)) {
      return true
    }
    if (data.demand_units_ytg && data.demand_units_ytg.KPI && data.demand_units_sas && data.demand_units_sas.KPI && varianceExceedsThreshhold(data.demand_units_ytg.KPI, data.demand_units_sas.KPI)) {
      return true
    }

    return false
  })
}
